            this.fecha = item.fecha
            this.folio = item.folio
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
          },

          editItem (item) {
            this.limpiar();
            this.eta = item.eta
            this.etd = item.etd
            this.procedencia = item.procedencia
            this.comentarios = item.comentarios
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
            this._id=item._id;
           
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

        activarDesactivarMostrar(accion,item,folio){
          this.adModal=1;
          this.adNombre=folio.folioId;
          this.folio = folio
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
            
          }else if(accion==3){
            this.adAccion=3
          }
          else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('solicitudUnica/activate',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre},configuracion)

               .then(function(response){
                
                me.listarEmbarcacion(response.data.embarcacion, response.data.procedencia,me.$store.state.usuario.nombre,response.data.comentarios,response.data.eta.toLocaleString(),response.data.etd.toLocaleString(),response.data.muelle,status)
                swal(
                  "Solicitud Confirmada",
                  "Solicitud confirmada con exito, en espera de aprobación de APITAB",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

         aprobarApi(item){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('solicitudUnica/aprobarApi',{'_id':this._id,'aprobacionApi':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                 me.ocultarComprobante()
                  swal(
                  "Solicitud Confirmada",
                  "Solicitud confirmada con exito por ausencia de APITAB",
                  "success"
                )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('solicitudUnica/deactivate',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre,'motivo_cancelacion':this.motivo_cancelacion},configuracion)
               .then(function(response){
                 me.listarEmbarcacionCancelada(response.data.embarcacion, response.data.procedencia,me.$store.state.usuario.nombre,response.data.comentarios,response.data.eta.toLocaleString(),response.data.etd.toLocaleString(),response.data.muelle,status,me.motivo_cancelacion)
                  swal(
                  "Solicitud Cancelada",
                  "Solicitud Cancelada con exito",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        activarDesactivarCerrar(){
          this.motivo_cancelacion=''
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>