            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
  
              <v-card-text>
                <v-container>
                  <v-row>
                   
                   
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="success" text @click="close">Salir</v-btn>
                <v-btn color="blue" text @click="guardar">Guardar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="adModal" max-width="290">
            <v-card>
              <v-card-title class="headline" v-if="adAccion==1">
                  Aprobar Solicitud
              </v-card-title>
              <v-card-title class="headline" v-if="adAccion==2">
                  Cancelar Solicitud
              </v-card-title>

              <v-card-text>
                  Estas a punto de <span v-if="adAccion==1">Aprobar</span>
                  <span v-if="adAccion==2">Cancelar</span> la solicitud con folio VP{{adNombre}}
              </v-card-text>
               <v-col cols="12" sm="12" md="12">
           <v-textarea
          name="input-7-1"
          label="Motivo de Cancelación"
          value=""
          v-if="adAccion==2"
          v-model="motivo_cancelacion"
        ></v-textarea>
        </v-col>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="activarDesactivarCerrar()" small color="error">
                  Salir
                </v-btn>
                 <v-btn v-if="adAccion==1" @click="aprobarApi()" small color="success">
