                                  <strong id="texto">Solicitud de Entrada</strong><br>
                                    <strong id="texto">VP{{agenciasUsuario.folio}}-{{counter}}</strong><br>

                              </p>
                          </div>
                          <br>
                        
                          <br>
                           <div id="texto" style="margin-left: 66%;">
                            Frontera, Tab. a: {{fecha}}
                          </div>
                           
                          
                        
                      </header>
                      <br>
                    <div id="agencia">
                        <p id="texto">De conformidad y en cumplimiento a lo estipulado en las Reglas de Operación de la APITAB en su capitulo IV.- "Accesi y
                          vialidades del Puerto" Regla 19, 20, 21 y 22 y Código Internacional de Protección a Buques e Instalaciones Portuarias.</p>  
                   
                      <p id="texto">Solicito la <strong>ENTRADA</strong> a las areas del recinto portuario concesionadas a la administracion portuaria 
                         integral de Tabasco S.A. de C.V con Fecha <strong>{{fecha_solicitud}}</strong> del personal y vehiculos que a continuacion se enlistan</p>   
                    </div>

                   <div id="agencia">
                      <div class="row">        
                        <v-col cols="4">
                          <v-subheader  class="pa-0">Nombre del Solicitante</v-subheader>
                            Cargo<br>
                            <v-subheader class="pa-0">Agencia Naviera</v-subheader>
                          
                        </v-col>
                        <v-col cols="8">
                          <v-text-field  class="pa-0" v-model="usuario.nombre" readonly  hide-details></v-text-field>
                          <v-text-field class="pa-0" v-model="usuario.cargo" readonly  hide-details></v-text-field>
                          <v-text-field class="pa-0" v-model="agenciasUsuario.nombre" readonly  hide-details></v-text-field>              
                        </v-col>
                      </div>
                             <table class="table table-sm">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Servicios portuarios requeridos</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                    <td >Prestador de Servicios</td>
                                    <td>
                                        <span v-if="tipo=='Prestador de Servicios'" style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="tipo=='Cesionario'" style="border-image: initial; color:white; border: 1px solid black;">______</span>                      

                                    </td>
                                    <td>Cesionario</td>
                                    <td>
                                        <span v-if="tipo=='Cesionario'"  style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="tipo=='Prestador de Servicios'" style="border-image: initial; color:white; border: 1px solid black;">______</span>                                                        </td>
                                 
                                  
                                </tr>
                                 
                                  <tr>      
                                   
                                  </tr>
                            
                              </tbody>
                              
                            </table>

                             

                                  
                             <table class="table table-sm">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="11">Características de la Embarcación</th>         
                                </tr>
                              </thead>
                              <tbody >
     
                                  <tr>
                                    <td colspan="3" style="border-color:white">Nombre de la embarcación</td>
                                    <td colspan="8">{{embarcacion.nombre}}</td>
                                    
                                 
                                  </tr>
                                   <tr>
                                    <td colspan="3" style="border-color:white"></td>
                                    <td colspan="8"></td>  
                                  </tr>
                            
                              </tbody>

                                <tbody style="border-color:white">
     
                                  <tr>
                                    <td colspan="3" style="border-color:white">Al servicio de la compañia</td>
                                    <td colspan="8">{{compania.razon_social}}</td>
                                    
                                 
                                  </tr>
                                   <tr>
                                    <td colspan="3" style="border-color:white"></td>
                                    <td colspan="8"></td>
                                    
                                 
                                  </tr>
                            
                              </tbody>

                              <tbody style="border-color:white">
                               
                               
                                  <tr>
                                    <td style="border-color:white">Tipo</td>
                                    <td>{{embarcacion.tipo}}</td>
                                    
                                    <td >Bandera</td>
                                    <td>{{embarcacion.bandera}}</td>
                                    <td >T.R.B.</td>
                                    <td>{{embarcacion.tbr}}</td>
                                    <td >Eslora</td>
                                     <td>{{embarcacion.eslora}}</td>
                                     <td >Manga</td>
                                     <td>{{embarcacion.manga}}</td>
                                    
                                    <td></td>
                                  </tr>
                                  <tr>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                     <td style="border-color:white"></td>
                                    <td></td>
                                  </tr>
                                  
                           

                           
                            
                              </tbody>
                            </table>
                                <table class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Lista de Personal</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                     <td >#</td>
                                    <td >Nombre</td>
                                    <td >Compañia</td>
                                    <td >Puesto</td>
                                    <td >No. de Licencia</td>
                                </tr>
                                <tr v-for="(det, index) in pasajeros" :key="det._id">   
                                          <td>{{index+1}}</td>
                                         
                                          <td>{{det.nombreCompleto}}</td>
                                           <td>{{det.compania}}</td>
                                           <td>{{det.puesto}}</td>
                                          <td></td>
                                      </tr>
                              </tbody>
                            </table>

                             

                            

                              <table  class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE ">
                                <tr>
                                  <th colspan="8">Vehiculos</th>         
                                </tr>
                              </thead>
                              <tbody >
                                <tr >
                                    
                                    <td >Marca</td>
                                    <td >Modelo</td>
                                    <td >Color</td>
                                    <td >Placas</td>
                                    <td >Compañia de aseguradora</td>
                                    <td >Vigencia de seguro</td>
                                </tr>
                                <tr>   
                                          <tr v-for="(det) in vehiculos" :key="det._id">   
                                         
                                         
                                          <td>{{det.marca}}</td>
                                           <td>{{det.tipo}}</td>
                                           <td>{{det.color}}</td>
                                             <td>{{det.placa}}</td>
                                           <td>{{det.compania_aseguradora}}</td>
                                           <td>{{det.vigenciaSeguro}}</td>
                                      </tr>
                                     
                              </tbody>
                            </table>

                             <table class="table table-sm">
                            
                              <tbody >
                                <tr>
                                    <td style="border-color:white" >Ingreso de material o equipo</td>
                                    <td style="border-color:white">
                                        <span v-if="ingreso_mat" style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="!ingreso_mat" style="border-image: initial; color:white; border: 1px solid black;">______</span>                      

                                    </td>
                                    <td style="border-color:white">Retiro de material o equipo</td>
                                    <td style="border-color:white">
                                        <span v-if="retiro_mat"  style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="!retiro_mat" style="border-image: initial; color:white; border: 1px solid black;">______</span>                                                        </td>
                                 
                                  
                                </tr>
                                 
                                  <tr style="border-color:white">       
                                   
                                  </tr>
                            
                              </tbody>
                              
                            </table>

                              <table class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Motivo de la visita</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                   
                                    <td >{{detalles}}</td>
                                  
                                </tr>
                               
                              </tbody>
                            </table>


                   </div> 
                 
                   
                  </div>
              </v-card-text>
              <v-card-actions>
                <v-btn @click="ocultarComprobante()" small>Cancelar</v-btn>
              </v-card-actions>
            </v-card>

          </v-dialog>
        </v-toolbar>
      </template>
       <template v-slot:item.fecha="{ item }">{{ item.fecha.join(' al ')}}</template>

      <template v-slot:item.action="{ item }">
      

         <template v-if="item.estado==1">
               <v-icon
      
          
          class="mr-2"
          @click="activarDesactivarMostrar(1,item)"
        >
          check
        </v-icon>
        </template>
          <v-icon
        v-if="item.estado==1 || item.estado==2 || item.estado==0"
          
          class="mr-2"
          @click="mostrarComprobante(item)"
        >
          print
        </v-icon>
         
           <v-icon
          v-if="item.estado==3 || item.estado==1"
          small
          @click="activarDesactivarMostrar(2,item)"
        >
          block
        </v-icon>
       

      </template>

      
        <template v-slot:item.manifiesto="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="download(item)"
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

        <template v-slot:item.personal_adj="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="downloadPersonal(item)" 
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

       <template v-slot:item.vehiculos_adj="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="downloadVehiculo(item)" 
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

    
      
   

      
      <template v-slot:no-data>
        <v-btn color="primary" @click="listar()">Resetear</v-btn>
      </template>
    </v-data-table>

    

  </v-flex>


  </v-layout>

  
</template>
<script>

import axios from 'axios';
import DatetimePicker from 'vuetify-datetime-picker'
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'
import swal from 'sweetalert'

  export default {
     icons: {
    iconfont: 'fa4',
  },
    data(){
      return{
                  dialog: false,
                  search: '',
                 cambiosGuardia:[],
                headers: [
               { text: 'Actions', value: 'action', sortable: false},
               { text: 'Estado', value: 'estado', sortable: false},
               { text: 'Fecha', value: 'fecha', sortable:true},
               { text: 'Embarcacion', value: 'embarcacion.nombre',sortable:false},
               { text: 'Muelle', value: 'muelle', sortable:false},
               { text: 'Manifiesto', value: 'manifiesto',sortable:false}, 
               { text: 'Adjuntos del Personal', value: 'personal_adj',sortable:false},
               { text: 'Adjuntos del Vehiculo', value: 'vehiculos_adj',sortable:false},  
                
            ],
            editedIndex: -1,
            _id:'',
            embarcacion:'',
            embarcaciones:[],
            muelles:['FRONTERA','CHILTEPEC','SANCHEZ MAGALLANES'],
            muelle:'',
            nombreCompleto:'',
            agencias:[],
            nombre:'',
            counter:'',
            tipo:'',
            tipo:'',
            compania:'',
            razon_social:'',
            retiro_mat:'',
            ingreso_mat:'',

            file3:'',
            agencia2:'',
            tipos:['Ayudante','Chofer'],
            tipoServicio:'',
            agenciasUsuario:[],
            cabeceraDetalles:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Nombre Completo', value: 'nombreCompleto' },
       
              { text: 'Tipo', value: 'tipo' },
           
            ],
            cabeceraVehiculos:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Placa', value: 'placa' },
              { text: 'Vigencia de Seguro', value: 'vigenciaSeguro' },
              { text: 'Color', value: 'color' },
              { text: 'Tipo', value: 'tipo' },
           
           
            ],

            adjuntos:[
            
              { text: 'Tarjeta de Circulacion', value: 'tarjetaCirculacion' },
              { text: 'Poliza', value: 'poliza' },
           
            ],
            pasajeros:[],
            vehiculos:[],
            verNuevo:0,
            usuario:'',
            fecha_solicitud:'',
            agencia:'',
            fecha:'',
            folio:'',
            fecha:'',
            estado:'',
            placa: '',
            prueba:[],
            file:'',
            vigenciaSeguro: '',
            color: '',
            tipoVehiculo:'',
            file2:'',
            targetaCirculacion:'',
            motivo_cancelacion:'',
            poliza:'',
            detalles:'',
            valida:0,
            fecha:null,
            validaMensaje:[],
            aprobacionAmls:'',
            aprobacionApi:'',
            adModal:0,
            comprobanteModal:0,
            adAccion:0,
            adNombre:'',
            adId:''
      }
    },
          computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'Nueva Solicitud' : 'Editar Solicitud'
          }
        },

        watch: {
          dialog (val) {
            val || this.close()
          }
        },

        created () {
          this.listar();
          this.selectEmbarcaciones();
          this.selectAgencias();
        },

        methods: {

          crearPDF(){
            var quotes = document.getElementById('solicitudGuardia')
             html2canvas(quotes).then(function (canvas) {
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 200;
                    var pageHeight = 305;

                    var imgHeight = canvas.height * imgWidth / canvas.width;
                    var heightLeft = imgHeight;
                    
                    var doc = new jsPDF('p', 'mm', 'a4');
                    var position = 0;

                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                    heightLeft -= pageHeight;

                    while (heightLeft >= 0) {
                        position = heightLeft - imgHeight;
                        doc.addPage();
                        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                        heightLeft -= pageHeight;
                    }
                    doc.save('solicitudCG.pdf');
            })
          },

          mostrarComprobante(item){
            this.limpiar();
            this.fecha = item.fecha
            this.counter = item.counter
            this.aprobacionAmls = item.aprobacionAmls
            this.detalles = item.detalles
            
            this.aprobacionApi = item.aprobacionApi
            this.usuario = item.usuario
            this.estado = item.estado
            this.muelle = item.muelle
           this.listarAgencia(item.usuario.agencia)
            var solic_fec = item.fecha;
            var fecha = new Date();
            var options = { year: 'numeric', month: 'long', day: 'numeric' };
            var fec = new Date(solic_fec);   

              var vigencia = new Date(item.fecha)
            this.compania = item.compania
            this.fechaActual = new Date();
            this.fechaFinal = vigencia
            this.ingreso_mat = item.ingreso_mat
            this.retiro_mat = item.retiro_mat
            this._id = item._id
             vigencia.setDate(vigencia.getDate()+3)
            this.vigencia = vigencia
            this.tipo  = item.tipo
            console.log(this.tipo)
            this.fecha_solicitud = item.fecha.join(" al ")
            this.fecha = fecha.toLocaleDateString("es-ES", options);
            this.embarcacion = item.embarcacion
            this.listarDetalles(item._id);
            this.listarDetallesVehiculos(item._id)
            this.comprobanteModal=1   
            
             var fechaAprobacion = item.fechaAprobacion
            var fechaActual = new Date()
            var fechaAprobacionDate = new Date(fechaAprobacion);
            var minutoSumar = 5;

            fechaAprobacionDate.setMinutes(fechaAprobacionDate.getMinutes() + minutoSumar);

            if(fechaActual>=fechaAprobacionDate){
                this.bandera = true
               
            }else{
              this.bandera = false
              
            }

            
          },
           ocultarComprobante(item){
            this.comprobanteModal=0
          },

          mostrarNuevo(){
            this.verNuevo =1;

          },
          ocultarNuevo(){
            this.verNuevo =0
            this.limpiar()
          },

          eliminarPasajero(arr,item){ 
            let i=arr.indexOf(item);
            if(i!= -1){
              arr.splice(i,1);
            }
          },

            eliminarVehiculo(arr,item){ 
            let i=arr.indexOf(item);
            if(i!= -1){
              arr.splice(i,1);
            }
          },

             listarAgencia(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('agencia/query?_id='+id,configuracion).then(function (response){
            me.agenciasUsuario=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },

          selectEmbarcaciones(){
            let me = this;
            let embarcacionArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('embarcacion/list',configuracion).then((response)=>{
              embarcacionArray=response.data;
              embarcacionArray.map(function(x){
                me.embarcaciones.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },

           selectAgencias(){
            let me = this;
            let agencaiArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('agencia/list',configuracion).then((response)=>{
              agencaiArray=response.data;
              agencaiArray.map(function(x){
                me.agencias.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },


          validar(){
            this.valida=0;
            this.validaMensaje=[];
            if(!this.embarcacion){
              this.validaMensaje.push('La embarcación es obligatorio');
            }
         
            if(!this.muelle){
              this.validaMensaje.push('El muelle es obligatoria');
            }
            
                   
            if(this.validaMensaje.length){
              this.valida=1;
            }
            return this.valida;
          },

          agregarDetalle(){
            this.pasajeros.push(
              {
                nombreCompleto: '',
                licencia: '',
                ine: '',
                tipo:''
              }
            )
          },

           agregarVehiculos(){
            this.vehiculos.push(
              {
                placa: '',
                vigenciaSeguro: '',
                color: '',
                tipoVehiculo:'',
                targetaCirculacion:'',
                poliza:''
              }
            )
          },

          limpiar(){
            this._id=''
           
            this.usuario='',
            this.folio='',
            this.agencia='',
            this.fecha='',

            this.pasajeros=[],
            this.vehiculos=[],
          
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
          },

          handleFileUpload(e){
              this.file = this.$refs.file.files[0];
              this.file2 = this.$refs.file1.files[0];
              this.file3 = this.$refs.file3.files[0];
              console.log(this.file3)
            },

          guardar(){
          
         
           let me=this;
            let header={"Token":this.$store.state.token};
            let formData3 = new FormData()
            formData3.append("file3",this.file3,this.file3.name)
            let formData2 = new FormData()
            formData2.append("file0",this.file2, this.file2.name)
            let formData = new FormData()
            formData.append("file1",this.file,this.file.name)
            let configuracion= {headers: header}
         

             if(this.editedIndex >-1 ){    
                axios.put('VehiculoPersonal/addVehiculos/'+this._id,formData)
               .then(function(response){
                 me.limpiar();
                 me.close();
                 me.listar();
               
               })
               .catch(function(error){
                 console.log(error)
               });
             }else{
                
                axios.post('vehiculoPersonal/add',
                 {
                  
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,
                    'muelle':this.muelle,
                    'fecha':this.fecha,
                    'pasajeros':this.pasajeros,
                    'vehiculos': this.vehiculos
                    
               },configuracion)
               .then(function(response){
                   
                   axios.put('vehiculoPersonal/update/'+response.data._id,formData)
                   axios.put('vehiculoPersonal/updateVehiculos/'+response.data._id,formData2)
                   axios.put('vehiculoPersonal/updatePersonal/'+response.data._id,formData3)
                 me.limpiar();
                me.ocultarNuevo();
                 me.close(); 
                 me.listar();
               
               }).catch(function(error){
                 console.log(error)
               });
              }
          },
          listar(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('vehiculoPersonal/list?valor='+this.$store.state.usuario._id,configuracion).then(function (response){
            me.cambiosGuardia=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },
        
         listarDetalles(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('vehiculoPersonal/query?_id='+id,configuracion).then(function (response){
            me.pasajeros=response.data.pasajeros;
            }).catch(function(error){
              console.log(error)
            })
          },

          listarDetallesVehiculos(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('vehiculoPersonal/query?_id='+id,configuracion).then(function (response){
            me.vehiculos=response.data.vehiculos;
            }).catch(function(error){
              console.log(error)
            })
          },
          verGuardia(item){
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.muelle = item.muelle
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
          },

          editItem (item) {
            this.fecha = item.fecha
          
             this.muelle = item.muelle
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
            this._id=item._id;
            this.folio = item.folio
            this.fecha = item.fecha
            this.detalles = item.detalles
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

             download(item){
             window.open(item.manifiesto, '_blank');
          },

              downloadPersonal(item){
              window.open(item.personal_adj, '_blank');
            },
          
             downloadVehiculo(item){
              window.open(item.vehiculos_adj, '_blank');
          },

        activarDesactivarMostrar(accion,item){
          this.adModal=1;
          this.adNombre=item.counter;
          this.adId=item._id;
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
          }else{
            this.adMmodal=0;
          }
        },
    activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('vehiculoPersonal/activate',{'_id':this.adId,'aprobacionAmls':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

          desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('vehiculoPersonal/deactivateApi',{'_id':this.adId,'aprobacionApi':this.$store.state.usuario.nombre,'motivo_cancelacion':this.motivo_cancelacion},configuracion)
               .then(function(response){
                 swal(
                   "Solicitud Cancelada",
                   "Solicitud cancelada con exito",
                   "success"
                 )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

         aprobarApi(item){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('vehiculoPersonal/aprobarApi',{'_id':this.adId,'aprobacionApi':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                swal(
                  "Solicitud Aprobada",
                  "Solicitud Aprobada con exito",
                  "success"
                )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>

