<template>
  <v-layout align-start>
    <v-flex>
      <v-data-table
        :headers="headers"
        :items="cambiosGuardia"
        :search="search"
        class="elevation-1"
        v-if="verNuevo==0"
      >
        <template v-slot:item.estado="{ item }">
          <v-chip small class="ma-2" color="orange" text-color="white" v-if="item.estado==0">
            <v-avatar left>
              <v-icon small>restore</v-icon>
            </v-avatar>En proceso
          </v-chip>

          <v-chip small class="ma-2" color="primary" text-color="white" v-if="item.estado==1">
            <v-avatar left>
              <v-icon small>done</v-icon>
            </v-avatar>Aprobado AMLS
          </v-chip>
          <v-chip small class="ma-2" color="success" text-color="white" v-if="item.estado==3">
            <v-avatar left>
              <v-icon small>done</v-icon>
            </v-avatar>Aprobado API
          </v-chip>
          <v-chip small class="ma-2" color="error" text-color="white" v-if="item.estado==2">
            <v-avatar left>
              <v-icon small>error</v-icon>
            </v-avatar>
            {{item.motivo_cancelacion}}
          </v-chip>
        </template>
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Cambios de Guardia</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-text-field
              class="text-xs-center"
              v-if="verNuevo==0"
              v-model="search"
              append-icon="search"
              label="Busqueda"
              single-line
              hide-details
            ></v-text-field>
            <v-spacer></v-spacer>
            <v-btn v-if="verNuevo==0" @click="mostrarNuevo()" small>Nueva Solicitud</v-btn>

            <v-dialog v-model="adModal" max-width="290">
              <v-card>
                <v-card-title class="headline" v-if="adAccion==1">Activar Item</v-card-title>
                <v-card-title class="headline" v-if="adAccion==2">Desactivar Item</v-card-title>

                <v-card-text>
                  Estas a punto de
                  <span v-if="adAccion==1">Activar</span>
                  <span v-if="adAccion==2">Desactivar</span>
                  la solicitud con folio {{adNombre}}
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    @click="activarDesactivarCerrar()"
                    color="green darken-1"
                    flat="flat"
                  >Cancelar</v-btn>
                  <v-btn
                    v-if="adAccion==1"
                    @click="activar()"
                    color="orange darken-4"
                    flat="flat"
                  >Activar</v-btn>
                  <v-btn
                    v-if="adAccion==2"
                    @click="desactivar()"
                    color="orange darken-4"
                    flat="flat"
                  >Desactivar</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="comprobanteModal" max-width="1100px">
              <v-card>
                <v-card-title class="headline">
                  <v-btn @click="crearPDF()">
                    <v-icon>print</v-icon>
                  </v-btn>Reporte de Entrada
                </v-card-title>
                <v-card-text>
                  <div id="solicitudGuardia">
                    <br />
                    <br />
                    <header>
                      <link
                        rel="stylesheet"
                        href="https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css"
                        integrity="sha384-Vkoo8x4CGsO3+Hhxv8T/Q5PaXtkKtu6ug5TOeNV6gBiFeWPGFN9MuhOf23Q9Ifjh"
                        crossorigin="anonymous"
                      />
                      <div id="logo">
                        <img
                          src="data:image/png;base64,"
                          id="imagen"
                        />
                      </div>
                      <div id="logo2">
                        <img
                          id="imagen2"
                          src="data:image/jpeg;base64,"
                        />
                      </div>
                      <div id="datos">
                        <p id="encabezado">
                          <strong id="texto">Solicitud de Entrada</strong>
                          <br />
                          <strong id="texto">CG{{agenciasUsuario.folio}}-{{folio}}</strong>
                          <br />
                        </p>
                      </div>
                      <br />

                      <br />
                      <div id="texto" style="margin-left: 66%;">
                        Frontera, Tab. a: {{fecha}}
                        <br />
                        <font size="3">
                          <strong>Solicitud de {{operacion}}</strong>
                        </font>
                      </div>
                    </header>
                    <br />
                    <div id="agencia">
                      <p id="texto">
                        De conformidad y en cumplimiento a lo estipulado en las Reglas de Operación de la APITAB en su capitulo IV.- "Accesi y
                        vialidades del Puerto" Regla 19, 20, 21 y 22 y Código Internacional de Protección a Buques e Instalaciones Portuarias.
                      </p>

                      <p id="texto">
                        Solicito la
                        <strong>ENTRADA</strong> a las areas del recinto portuario concesionadas a la administracion portuaria
                        integral de Tabasco S.A. de C.V con Fecha
                        <strong>{{fecha_solicitud}}</strong> del personal y vehiculos que a continuacion se enlistan
                      </p>
                    </div>

                    <div id="agencia">
                      <div class="row">
                        <v-col cols="4">
                          <v-subheader class="pa-0">Nombre del Solicitante</v-subheader>Cargo
                          <br />
                          <v-subheader class="pa-0">Agencia Naviera</v-subheader>
                        </v-col>
                        <v-col cols="8">
                          <v-text-field class="pa-0" v-model="usuario.nombre" readonly hide-details></v-text-field>
                          <v-text-field class="pa-0" v-model="usuario.cargo" readonly hide-details></v-text-field>
                          <v-text-field
                            class="pa-0"
                            v-model="agenciasUsuario.nombre"
                            readonly
                            hide-details
                          ></v-text-field>
                        </v-col>
                      </div>
                      <table class="table table-sm">
                        <thead style="background-color:#EFEEEE">
                          <tr>
                            <th colspan="8">Servicios portuarios requeridos</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>Contratista</td>
                            <td>
                              <span
                                v-if="tipo=='Contratista'"
                                style="border-image: initial;color:black;  background-color:black "
                              >______</span>
                              <span
                                v-if="tipo=='Armador'"
                                style="border-image: initial; color:white; border: 1px solid black;"
                              >______</span>
                            </td>
                            <td>Armador</td>
                            <td>
                              <span
                                v-if="tipo=='Armador'"
                                style="border-image: initial;color:black;  background-color:black "
                              >______</span>
                              <span
                                v-if="tipo=='Contratista'"
                                style="border-image: initial; color:white; border: 1px solid black;"
                              >______</span>
                            </td>
                          </tr>

                          <tr></tr>
                        </tbody>
                      </table>

                      <table class="table table-sm">
                        <thead style="background-color:#EFEEEE">
                          <tr>
                            <th colspan="11">Características de la Embarcación</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td colspan="3" style="border-color:white">Nombre de la embarcación</td>
                            <td colspan="8">{{embarcacion.nombre}}</td>
                          </tr>
                          <tr>
                            <td colspan="3" style="border-color:white"></td>
                            <td colspan="8"></td>
                          </tr>
                        </tbody>

                        <tbody style="border-color:white">
                          <tr>
                            <td colspan="3" style="border-color:white">Al servicio de la compañia</td>
                            <td colspan="8">{{compania.razon_social}}</td>
                          </tr>
                          <tr>
                            <td colspan="3" style="border-color:white"></td>
                            <td colspan="8"></td>
                          </tr>
                        </tbody>

                        <tbody style="border-color:white">
                          <tr>
                            <td style="border-color:white">Tipo</td>
                            <td>{{embarcacion.tipo}}</td>

                            <td>Bandera</td>
                            <td>{{embarcacion.bandera}}</td>
                            <td>T.R.B.</td>
                            <td>{{embarcacion.tbr}}</td>
                            <td>Eslora</td>
                            <td>{{embarcacion.eslora}}</td>
                            <td>Manga</td>
                            <td>{{embarcacion.manga}}</td>

                            <td></td>
                          </tr>
                          <tr>
                            <td style="border-color:white"></td>
                            <td></td>

                            <td style="border-color:white"></td>
                            <td></td>
                            <td style="border-color:white"></td>
                            <td></td>
                            <td style="border-color:white"></td>
                            <td></td>
                            <td style="border-color:white"></td>
                            <td></td>
                          </tr>
                        </tbody>
                      </table>
                      <table class="table table-sm table-bordered">
                        <thead style="background-color:#EFEEEE">
                          <tr>
                            <th colspan="8">Lista de Personal</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>#</td>
                            <td>Nombre</td>
                            <td>Compañia</td>
                            <td>Puesto</td>
                            <td>No. de Licencia</td>
                          </tr>
                          <tr v-for="(det, index) in pasajeros" :key="det._id">
                            <td>{{index+1}}</td>

                            <td>{{det.nombreCompleto}}</td>
                            <td>{{det.compania}}</td>
                            <td>{{det.puesto}}</td>
                            <td></td>
                          </tr>
                        </tbody>
                      </table>

                      <table class="table table-sm table-bordered">
                        <thead style="background-color:#EFEEEE ">
                          <tr>
                            <th colspan="8">Vehiculos</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>Marca</td>
                            <td>Modelo</td>
                            <td>Color</td>
                            <td>Placas</td>
                            <td>Compañia de aseguradora</td>
                            <td>Vigencia de seguro</td>
                          </tr>
                          <tr>
                            <td></td>

                            <td style="color:white">_</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                          </tr>
                        </tbody>
                      </table>

                      <table class="table table-sm">
                        <tbody>
                          <tr>
                            <td style="border-color:white">Ingreso de material o equipo</td>
                            <td style="border-color:white">
                              <span
                                v-if="ingreso_mat"
                                style="border-image: initial;color:black;  background-color:black "
                              >______</span>
                              <span
                                v-if="!ingreso_mat"
                                style="border-image: initial; color:white; border: 1px solid black;"
                              >______</span>
                            </td>
                            <td style="border-color:white">Retiro de material o equipo</td>
                            <td style="border-color:white">
                              <span
                                v-if="retiro_mat"
                                style="border-image: initial;color:black;  background-color:black "
                              >______</span>
                              <span
                                v-if="!ingreso_mat"
                                style="border-image: initial; color:white; border: 1px solid black;"
                              >______</span>
                            </td>
                          </tr>

                          <tr style="border-color:white"></tr>
                        </tbody>
                      </table>
              
                      <table class="table table-sm table-bordered">
                        <thead style="background-color:#EFEEEE">
                          <tr>
                            <th colspan="8">Motivo de la visita</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>{{detalles}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-btn @click="ocultarComprobante()" small>Salir</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="comprobanteModalPasajero" max-width="1000px">
              <v-card>
                <v-card-title class="headline">
                  <v-btn @click="crearPDF()">
                    <v-icon>print</v-icon>
                  </v-btn>Reporte de Entrada
                </v-card-title>
                <v-card-text>
                  <div id="solicitudGuardia">
                    <br />
                    <br />
                    <header>
                      <link
                        rel="stylesheet"
                        href="https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css"
                        integrity="sha384-Vkoo8x4CGsO3+Hhxv8T/Q5PaXtkKtu6ug5TOeNV6gBiFeWPGFN9MuhOf23Q9Ifjh"
                        crossorigin="anonymous"
                      />
                      <div id="logo">
                        <img
                          src="data:image/png;base64,"
                          id="imagen"
                        />
                      </div>
                      <div id="logo2">
                        <img
                          id="imagen2"
                          src="data:image/jpeg;base64,"
                        />
                      </div>
                      <div id="datos">
                        <p id="encabezado">
                          <strong>Aviso de Entrada</strong>
                          <br />Folio:
                          <strong>CG{{agenciasUsuario.folio}}-{{folio}}</strong>
                          <br />
                        </p>
                      </div>
                      <br />
                      <div></div>
                      <br />
                      <div style="margin-left: 66%;">Frontera, Tab. a: {{fecha}}</div>
                    </header>
                    <br />

                    <section>
                      <table class="table table-sm">
                        <thead class="thead-dark">
                          <tr>
                            <th colspan="8">Pasajeros</th>
                          </tr>
                        </thead>
                        <thead>
                          <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nombre</th>
                            <th scope="col">RFC</th>
                            <th scope="col">Libreta de Mar</th>
                            <th scope="col">NSS</th>
                            <th scope="col">Destino</th>
                            <th scope="col">Emb/Des</th>
                            <th scope="col">Estatus</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(det, index) in pasajeros" :key="det._id">
                            <td>{{index+1}}</td>
                            <td>{{det.nombreCompleto}}</td>
                            <td>{{det.rfc}}</td>
                            <td>{{det.identificacion}}</td>
                            <td>{{det.nss}}</td>
                            <td>{{det.destino}}</td>
                            <td>{{det.estatus}}</td>
                            <td>{{det.estado}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </section>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-btn @click="ocultarComprobante()" small>Salir</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon v-if="item.estado==0" small class="mr-2" @click="editItem(item)">edit</v-icon>

          <v-icon v-if="item.estado==2" class="mr-2" @click="editItem(item)">edit</v-icon>

          <v-icon v-if="item.estado==3" class="mr-2" @click="mostrarComprobante(item)">text_snippet</v-icon>
        </template>
        <template v-slot:item.adjunto="{ item }">
          <v-icon small class="mr-2" @click="descargarCG(item)">fa fa-file-pdf-o</v-icon>
        </template>
}
        <template v-slot:item.list_p="{ item }">
          <v-icon small class="mr-2" @click="mostrarComprobantePasajero(item)">fa fa-file-pdf-o</v-icon>
        </template>

        <template v-slot:item.fecha="{ item }">{{ item.fecha.join(' al ')}}</template>

        <template v-slot:item.folio="{ item }">CG{{ item.folio}}</template>

        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Resetear</v-btn>
        </template>
      </v-data-table>

      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-dialog v-model="dialog" max-width="1000px">
         
          <v-card>
            <v-card-title>
              <span>Seleccione el pasajero</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-text-field
                      v-model="texto"
                      @keyup.enter="listarPasajeros()"
                      class="text-xs-center"
                      append-icon="search"
                      label="Busqueda"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraPasajeros"
                        :items="passenger"
                        class="elevation-1"
                      >
                        <template v-slot:top>
                          <v-toolbar flat color="white">
                            <v-toolbar-title>Mis Pasajeros</v-toolbar-title>

                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.action="{ item }">
                          <template>
                            <v-icon @click="agregarDetalle(item)">add</v-icon>
                          </template>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Salir</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-layout row wrap>
          <v-flex xs11 sm11 lg11 x11>
            <div>
              <v-expansion-panels v-model="panel" multiple>
                <v-expansion-panel>
                  <v-expansion-panel-header>Datos de la Agencia</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-row no-gutters>
                      <v-col cols="4">
                        <v-subheader class="pa-0">Agencia Consignataria</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-text-field
                          class="pa-0"
                          v-model="agenciasUsuario.nombre"
                          readonly
                          hide-details
                        ></v-text-field>
                      </v-col>

                      <v-col cols="4">
                        <v-subheader class="pa-0">Direccion</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-text-field
                          class="pa-0"
                          v-model="agenciasUsuario.direccion"
                          readonly
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col cols="4">
                        <v-subheader class="pa-0">RFC</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-text-field
                          class="pa-0"
                          v-model="agenciasUsuario.rfc"
                          readonly
                          hide-details
                        ></v-text-field>
                      </v-col>

                      <v-col cols="4">
                        <v-subheader class="pa-0">Email</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-text-field
                          class="pa-0"
                          v-model="agenciasUsuario.email"
                          readonly
                          hide-details
                        ></v-text-field>
                      </v-col>

                      <v-flex xs4 sm14 lg4 x4>
                        <v-radio-group v-model="tipo" row>
                          <v-radio label="Contratista" value="Contratista"></v-radio>
                          <v-radio label="Armador" value="Armador"></v-radio>
                        </v-radio-group>
                      </v-flex>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>Datos de la solicitud</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-row no-gutters>
                      <v-col cols="4">
                        <v-subheader class="pa-0">Periodo de la estadia</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-flex xs12 sm12 lg12 x12>
                          <v-dialog
                            ref="dialog"
                            v-model="modal"
                            :return-value.sync="date"
                            persistent
                            width="290px"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="dateRangeText"
                                prepend-icon="event"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                                class="pa-0"
                                hide-details
                              ></v-text-field>
                            </template>
                            <v-date-picker v-model="dates" range scrollable>
                              <v-spacer></v-spacer>
                              <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                              <v-btn text color="primary" @click="$refs.dialog.save(date)">OK</v-btn>
                            </v-date-picker>
                          </v-dialog>
                        </v-flex>
                      </v-col>

                      <v-col cols="4">
                        <v-subheader class="pa-0">Muelle de Arribo</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-autocomplete
                          :items="muelles"
                          v-on="on"
                          class="pa-0"
                          hide-details
                          v-model="muelle"
                        ></v-autocomplete>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-checkbox
                          class="pa-0"
                          hide-details
                          label="Ingreso de Material"
                          v-model="ingreso_mat"
                        ></v-checkbox>
                        <v-checkbox
                          class="pa-0"
                          hide-details
                          label="Retiro de Material"
                          v-model="retiro_mat"
                        ></v-checkbox>
                      </v-col>

                      <v-flex xs4 sm14 lg4 x4>
                        <v-radio-group v-model="operacion" row>
                          <v-radio label="Embarque" value="Embarque"></v-radio>
                          <v-radio label="Desembarque" value="Desembarque"></v-radio>
                        </v-radio-group>
                      </v-flex>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>
                <v-expansion-panel>
                  <v-expansion-panel-header>Características de la embarcacion</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-row no-gutters>
                      <v-col cols="4">
                        <v-subheader class="pa-0">Nombre de la embarcación</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-autocomplete
                          v-model="embarcacion"
                          class="pa-0"
                          hide-details
                          :items="embarcaciones"
                        ></v-autocomplete>
                      </v-col>

                      <v-col cols="4">
                        <v-subheader class="pa-0">Al servicio de la compañia</v-subheader>
                      </v-col>

                      <v-col cols="8" class="text--secondary">
                        <v-autocomplete
                          :items="companias"
                          class="pa-0"
                          hide-details
                          v-model="compania"
                        ></v-autocomplete>
                      </v-col>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>Lista del Personal</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-row no-gutters>
