</template>
<script>
import axios from "axios";
import DatetimePicker from "vuetify-datetime-picker";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import emailjs from "emailjs-com";
import S3 from "aws-s3";
import swal from "sweetalert";

export default {
  icons: {
    iconfont: "fa4"
  },

  data() {
    return {
      headers: [
        { text: "Actions", value: "action", sortable: false },
        { text: "Fecha", value: "fecha", sortable: true },
        { text: "Folio", value: "folio", sortable: false },
        { text: "Embarcacion", value: "embarcacion.nombre", sortable: false },
        { text: "Muelle", value: "muelle", sortable: false },
        { text: "Estatus", value: "estado", sortable: true },
        { text: "Adjuntos", value: "adjunto", sortable: false },
        { text: "Lista de Pasajeros", value: "list_p", sortable: false }
      ],
      editedIndex: -1,
      _id: "",
      embarcacion: "",
      embarcaciones: [],
      dialog: "",
      search: "",
      cambiosGuardia: [],
      muelles: ["FRONTERA", "CHILTEPEC", "SANCHEZ MAGALLANES"],
      muelle: "",

      menu: false,
      modal: false,
      menu2: false,
      alert: false,

      nombreCompleto: "",
      agencias: [],
      nombre: "",
      compania: "",
      companias: [],
      barcodeValue: "",
      operacion: "",
      agencia2: "",
      tipoServicio: "",
      agenciasUsuario: [],
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "RFC", value: "rfc" },
        { text: "Nombre Completo", value: "nombreCompleto" },
        { text: "Compañia", value: "compania" },
        { text: "Puesto", value: "puesto" },

        { text: "Destino", value: "destino" }
      ],
      pasajeros: [],
      panel: [0, 1, 2, 3],
      verNuevo: 0,
      usuario: "",
      operacion: "",
      passenger: [],
      texto: [],
      cabeceraPasajeros: [
        { text: "Seleccion", value: "action", sortable: false },
        { text: "RFC", value: "rfc", sortable: true },
        { text: "Nombre Completo", value: "nombre_completo", sortable: true },
        {
          text: "Numero de Libreta de Mar",
          value: "numero_libreta",
          sortable: true
        },
        { text: "Numero de Seguro Social", value: "nss", sortable: false }
      ],

      fecha_solicitud: "",
      agencia: "",
      fecha: "",
      adjunto: "",
      errorRfc: null,
      folio: "",
      fecha: "",
      dialogs: false,
      motivo_cancelacion: "",
      dates: [],
      estado: "",
      estatus: "Embarque",
      emb_des: ["Embarque", "Desembarque"],
      tipoServicio: "",
      detalles: "",
      rfc: "",
      valida: 0,
      file: "",
      ingreso_mat: false,
      retiro_mat: false,
      tipoServ: "CAM",
      tipo: "",
      fecha: null,
      date: "",
      validaMensaje: [],
      aprobacionAmls: "",
      aprobacionApi: "",
      adModal: 0,
      comprobanteModal: 0,
      comprobanteModalPasajero: 0,
      adAccion: 0,
      adNombre: "",
      adjunto: "",
      adId: ""
    };
  },
  computed: {
    config() {
      return {
        bucketName: "sam-amls",
        dirName: "cg" /* optional */,
        region: "us-west-1",
        accessKeyId: "AKIAVFDVDRT4HCGVWS6A",
        secretAccessKey: "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",
        s3Url: this.baseUrl
      };
    },
    baseUrl() {
      return "https://sam-amls.s3-us-west-1.amazonaws.com";
    },
    S3Client() {
      return new S3(this.config);
    },
    newFileName() {
      return Math.random()
        .toString()
        .slice(2);
    },

    url() {
      return `${this.baseUrl}/${"cg"}/${this.newFileName}`;
    },

    dateRangeText() {
      return this.dates.join(" al ");
    },
    formTitle() {
      return this.editedIndex === -1 ? "Nueva Solicitud" : "Editar Solicitud";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.listar();
    this.selectEmbarcaciones();
    this.selectAgencias();
    this.emailIn();
    this.selectCompanias();
  },

  methods: {
    crearPDF() {
      var quotes = document.getElementById("solicitudGuardia");
      html2canvas(quotes).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 210;
        var pageHeight = 295;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 6, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("solicitudCG.pdf");
      });
    },

    emailIn() {
      emailjs.init("user_cYfj7TRmYpJ0a9f5te9zQ");
    },
    enviar(
      fecha,
      embarcacion,
      muelle,
      detalles,
      tipoServicio,
      usuario,
      status
    ) {
      var template_params = {
        fecha: fecha,
        embarcacion: embarcacion,
        muelle: muelle,
        detalles: detalles,
        tipoServicio: tipoServicio,
        usuario: usuario,
        status: status
      };

      emailjs.send("gmail", "confirmacion", template_params).then(
        function(response) {
          if (response.text === "OK") {
          }
          console.log(
            "SUCCESS. status=%d, text=%s",
            response.status,
            response.text
          );
        },
        function(err) {
          alert("Ocurrió un problema al enviar el correo");
          console.log("FAILED. error=", err);
        }
      );
    },

    mostrarComprobante(item) {
      this.limpiar();
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.aprobacionAmls = item.aprobacionAmls;
      this.detalles = item.detalles;
      if (item.tipo == "Contratista") {
        this.tipo = "Contratista";
      } else {
        this.tipo = "Armador";
      }
      this.ingreso_mat = item.ingreso_mat;
      this.retiro_mat = item.retiro_mat;
      this.compania = item.compania;
      this.aprobacionApi = item.aprobacionApi;
      this.usuario = item.usuario;
      this.fecha_solicitud = item.fecha.join(" al ");
      this.tipoServicio = item.tipoServicio;
      this.estado = item.estado;
      this.muelle = item.muelle;
      this.listarAgencia(item.usuario.agencia);
      var fecha = new Date();
      var options = { year: "numeric", month: "long", day: "numeric" };
      this.operacion = item.operacion;
      this.fecha = fecha.toLocaleDateString("es-ES", options);
      this.embarcacion = item.embarcacion;
      this.listarDetalles(item._id);
      this.comprobanteModal = 1;
    },

    mostrarComprobantePasajero(item) {
      this.limpiar();

      this.listarAgencia(item.usuario.agencia);
      this.folio = item.folio;
      var solic_fec = item.fecha;
      var fecha = new Date();
      var options = { year: "numeric", month: "long", day: "numeric" };
      var fec = new Date(solic_fec);
      this.fecha_solicitud = fec.toLocaleString();
      this.fecha = fecha.toLocaleDateString("es-ES", options);
      this.embarcacion = item.embarcacion;
      this.listarDetalles(item._id);
      this.comprobanteModalPasajero = 1;
    },

    ocultarComprobante(item) {
      this.comprobanteModal = 0;
    },

    mostrarNuevo() {
      this.listarAgencia(this.$store.state.usuario.agencia);
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },

    eliminarPasajero(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },

    listarPasajeros() {
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let me = this;
      axios
        .get(
          "pasajeros/list?valor=" +
            this.texto +
            "&agencia=" +
            this.$store.state.usuario.agencia,
          configuracion
        )
        .then(function(response) {
          me.passenger = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    listarAgencia(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("agencia/query?_id=" + id, configuracion)
        .then(function(response) {
          me.agenciasUsuario = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    selectEmbarcaciones() {
      let me = this;
      let embarcacionArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("embarcacion/listActivos", configuracion)
        .then(response => {
          embarcacionArray = response.data;
          embarcacionArray.map(function(x) {
            me.embarcaciones.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    selectCompanias() {
      let me = this;
      let companiaArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get(
          "companias/list?agencia=" + this.$store.state.usuario.agencia,
          configuracion
        )
        .then(response => {
          companiaArray = response.data;
          companiaArray.map(function(x) {
            me.companias.push({ text: x.razon_social, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    selectAgencias() {
      let me = this;
      let agencaiArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("agencia/list", configuracion)
        .then(response => {
          agencaiArray = response.data;
          agencaiArray.map(function(x) {
            me.agencias.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    guardiaVisitante() {
      if (this.tipoServicio == "Cambio de Guardia") {
        this.cabeceraDetalles[2].text = "N° de Libreta de Mar";
      } else {
        this.cabeceraDetalles[2].text = "Identificacion";
      }
    },

    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.embarcacion) {
        this.validaMensaje.push("La embarcación es obligatorio");
      }

      if (!this.muelle) {
        this.validaMensaje.push("El muelle es obligatoria");
      }

      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    agregarDetalle(data) {
      this.alert = true;
      this.errorRfc = null;
      if (this.encuentra(data.rfc) == true) {
        swal(
          "Error",
          "El pasajero ya ah sido agregado",
          "error"
        )
      } else {
        this.pasajeros.push({
          rfc: data.rfc,
          nombreCompleto: data.nombre_completo,
          identificacion: data.numero_libreta,
          nss: data.nss,
          compania: data.compania,
          puesto: data.puesto,
          destino: "",
          estatus: ""
        });
        swal(
          "Se ah ingresado el Pasajero",
          "Se agregado exitosamente al pasajero",
          "success"
        )
      }
    },
    mostrarModalPasajero() {
      this.dialog = 1;
    },
    encuentra(id) {
      let sw = 0;
      for (var i = 0; i < this.pasajeros.length; i++) {
        if (this.pasajeros[i].rfc == id) {
          sw = true;
        }
      }
      return sw;
    },
    limpiar() {
      this._id = "";

      (this.usuario = ""),
        (this.folio = ""),
        (this.agencia = ""),
        (this.fecha = ""),
        (this.pasajeros = []),
        (this.valida = 0),
        (this.validaMensaje = []),
        (this.editedIndex = -1);
    },
    handleFileUpload(e) {
      this.file = this.$refs.file1.files[0];
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };

      if (this.editedIndex > -1) {
        if (me.file) {
          let file = me.file;
          let fileExtension = file.type.split("/")[1];
          this.adjunto = `${me.url}.${fileExtension}`;
        }

        axios
          .put(
            "cambioGuardia/update",
            {
              _id: this._id,
              embarcacion: this.embarcacion,
              usuario: this.$store.state.usuario._id,
              muelle: this.muelle,
              folio: this.folio,
              detalles: this.detalles,
              fecha: this.dates,
              pasajeros: this.pasajeros,
              adjunto: this.adjunto,
              tipo: this.tipo,
              compania: this.compania,
              ingreso_mat: this.ingreso_mat,
              retiro_mat: this.retiro_mat,
              operacion: this.operacion
            },
            configuracion
          )
          .then(function(response) {
            if (me.file) {
              me.S3Client.uploadFile(me.file, me.newFileName);
            }
            swal(
              "Solicitud de Embarque/Desembarque de Personal Actualizada",
              "Solicitud de acceso al muelle en espera de aprobación por AMLS y APITAB",
              "success"
            );
            me.ocultarNuevo();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            swal(
              "Error",
              "Error al guardar la solicitud, veriificar los datos ingresados",
              "error"
            );
            me.ocultarNuevo();
          });
      } else {
        if (me.file) {
          let file = me.file;
          let fileExtension = file.type.split("/")[1];
          this.adjunto = `${me.url}.${fileExtension}`;
        }

        axios
          .post(
            "cambioGuardia/add",
            {
              embarcacion: this.embarcacion,
              usuario: this.$store.state.usuario._id,
              folio: this.folio,
              muelle: this.muelle,
              detalles: this.detalles,
              fecha: this.dates,
              pasajeros: this.pasajeros,
              detalles: this.detalles,
              adjunto: this.adjunto,
              tipo: this.tipo,
              compania: this.compania,
              ingreso_mat: this.ingreso_mat,
              retiro_mat: this.retiro_mat,
              operacion: this.operacion
            },
            configuracion
          )
          .then(function(response) {
            if (me.file) {
              me.S3Client.uploadFile(me.file, me.newFileName);
            }
            swal(
              "Nueva solicitud de Embarque/Desembarque de Personal FOLIO:" +
                " CG" +
                response.data.folio,
              "Nueva solicitud de acceso al muelle en espera de aprobación por AMLS y APITAB",
              "success"
            );
            me.close();
            me.listarEmbarcacion(
              response.data.fecha.toLocaleString(),
              response.data.embarcacion,
              response.data.muelle,
              response.data.detalles,
              me.$store.state.usuario.nombre
            );
            me.ocultarNuevo();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            swal(
              "Error",
              "Error al guardar la solicitud, veriificar los datos ingresados",
              "error"
            );
            me.ocultarNuevo();
          });
      }
    },
    descargarCG(item) {
      window.open(item.adjunto, "_blank");
    },
    listar() {
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let me = this;
      axios
        .get(
          "cambioGuardia/list?valor=" + this.$store.state.usuario._id,
          configuracion
        )
        .then(function(response) {
          me.cambiosGuardia = response.data;
          
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    buscarRfc() {
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let me = this;
      
      axios
        .get("pasajeros/busquedaRfc?rfc=" + this.rfc, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
          console.log("sss")
        })
        .catch(function(error) {
          swal(
            "Error",
            "El RFC ingresado no existe, verifique los datos ingresados",
            "error"
          )
        });
    },

    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("cambioGuardia/query?_id=" + id, configuracion)
        .then(function(response) {
          me.pasajeros = response.data.pasajeros;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    listarEmbarcacion(fecha, embar, muelle, detalles, tipoServicio, usuario) {
      let header = { Token: this.$store.state.token };
      let me = this;
      let embarcacion;

      let configuracion = { headers: header };
      axios
        .get("embarcacion/query?_id=" + embar, configuracion)
        .then(function(response) {
          embarcacion = response.data;
          var fechaLocal;
          status = "en proceso";

          var fec = new Date(fecha);
          fechaLocal = fec.toLocaleString();
          me.enviar(fechaLocal, embarcacion, muelle, detalles, usuario, status);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    verGuardia(item) {
      this.limpiar();
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.muelle = item.muelle;
      this.detalles = item.detalles;
      this.agencia = item.agencia._id;
      this.embarcacion = item.embarcacion._id;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
    },

    editItem(item) {
      this.limpiar();
      this.listarAgencia(this.$store.state.usuario.agencia);
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.detalles = item.detalles;
      this.muelle = item.muelle;
      this.embarcacion = item.embarcacion._id;
      this.dates = item.fecha;
      this.tipo = item.tipo;
      this.compania = item.compania._id;
      this.operacion = item.operacion;
      this.retiro_mat = item.retiro_mat;
      this.ingreso_mat = item.ingreso_mat;

      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this._id = item._id;
      this.folio = item.folio;
      this.fecha = item.fecha;
      this.detalles = item.detalles;
      this.editedIndex = 1;
      this.dialog = false;
      this.verNuevo = 1;
    },

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.nombre;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adMmodal = 0;
      }
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("usuario/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("usuario/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },

    close() {
      this.dialog = false;
    }
  }
};
</script>

