        })
        .catch(function(error) {
          console.log(error);
        });
    },
    verGuardia(item) {
      this.limpiar();
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.detalles = item.detalles;
      this.agencia = item.agencia._id;
      this.embarcacion = item.embarcacion._id;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
    },

    editItem(item) {
      this.limpiar();
      this.eta = item.eta;
      this.etd = item.etd;
      this.procedencia = item.procedencia;
      this.comentarios = item.comentarios;
      this.agencia = item.agencia._id;
      this.embarcacion = item.embarcacion._id;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this._id = item._id;

      this.editedIndex = 1;
      this.dialog = false;
      this.verNuevo = 1;
    },

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.folio_solicitud;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adMmodal = 0;
      }
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put(
          "solicitudUnica/deactivateApi",
          {
            _id: this.adId,
            aprobacionApi: this.$store.state.usuario.nombre,
            motivo_cancelacion: this.motivo_cancelacion
          },
          configuracion
        )
        .then(function(response) {
          me.enviar("rechazado");
          swal(
            "Solicitud Cancelada",
            "Solicitud Cancelada con exito",
            "success"
          );
          me.ocultarComprobante();
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
