<template>
  <div align="center">

        <input type="file"  id="file" ref="file" name="file0" v-on:change="handleFileUpload()" label="File input"/>
        <v-btn @click="guardar()" text small color="primary">Primary</v-btn>
  </div>

</template>
<script>
import axios from 'axios'

  export default {

    data(){
      return{
        adjunto:[],
        archivo:'',
        file:''
      }
    },
    methods: {
      
      guardar(){
            let me=this;
            let header={"Token":this.$store.state.token};
            this.file= this.$refs.file.files[0]
            
            let config = {'Content-Type': 'multipart/form-data'}
            let formData = new FormData()
            formData.append("file0",this.file,this.file.name)

             if(this.editedIndex >-1 ){    
                axios.put('agencia/upload',{'_id':this._id,formData},{
                  headers: {
                    'Content-Type': 'multipart/form-data '
                  }
                })
               .then(function(response){ 
                 me.limpiar();
                 me.close();
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
             }else{
                
                axios.post('agencia/add',{'nombre':this.nombre,'rfc':this.rfc,'representante':this.representante,'folio':this.folio,'archivo':formData})
               .then(function(response){
                 
                 me.limpiar();
                 me.close();
                 me.listar();
               
               }).catch(function(error){
                 console.log(error)
               });
               }
               
               
             
          },
       handleFileUpload(e){
             this.file = this.$refs.file.files[0];
             console.log(this.file)
            },
    }
  };
</script>