            ></v-select>
        </v-col>   

          <v-container>
            <v-row>
                    <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Usuario' || reporteCategoria=='Por Fecha' || reporteCategoria=='Por Embarcacion' || reporteCategoria=='Por Estatus'  || reporteCategoria=='Por Muelle' || reporteCategoria=='Servicios Por Tipo' || reporteCategoria=='Servicios Por Muelle'">
                        <v-datetime-picker label="Fecha Inicial" locale="es" v-model="fecha1"> </v-datetime-picker>
                    </v-col>
                       <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Usuario' || reporteCategoria=='Por Fecha'  || reporteCategoria=='Por Embarcacion' || reporteCategoria=='Por Estatus' || reporteCategoria=='Por Muelle' || reporteCategoria=='Servicios Por Tipo' || reporteCategoria=='Servicios Por Muelle'">
                        <v-datetime-picker label="Fecha Final" locale="es" v-model="fecha2"> </v-datetime-picker>
                    </v-col>
                    <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Usuario'">
                        <v-autocomplete  v-model="usuario" :items="usuarios"  label="Usuarios"></v-autocomplete>
                    </v-col>
                    <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Embarcacion'">
                        <v-autocomplete  v-model="embarcacion" :items="embarcaciones"  label="Embarcacion"></v-autocomplete>
                    </v-col>
                     <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Servicios Por Agencia'">
                        <v-autocomplete  v-model="agencia" :items="agencias"  label="Agencia"></v-autocomplete>
                    </v-col>
                      <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Estatus'">
                        <v-autocomplete  v-model="estado" :items="estados"  label="Estado"></v-autocomplete>
                    </v-col>
                      <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Por Muelle'">
                        <v-autocomplete  v-model="muelle" :items="muelles"  label="Muelle"></v-autocomplete>
                    </v-col>
                      <v-col cols="4" sm="4" md="4" v-if="reporteCategoria=='Servicios Por Muelle'">
                        <v-text-field v-model="muelle" label="Ingresa el nombre del muelle"></v-text-field>
                      </v-col>
            </v-row>
          </v-container>

     
    <v-btn
      :loading="loading"
      :disabled="loading"
      small
      color="blue-grey"
      class="ma-2 white--text"
      @click="loader = 'loading'"
    >
      Generar Reporte
      <v-icon right dark>print</v-icon>
    </v-btn>

         <v-dialog v-model="comprobanteModal" max-width="1000px">
            <v-card>
              <v-card-title class="headline" >
                  <v-btn @click="crearPDF()"><v-icon>print </v-icon> </v-btn> Reportes
              </v-card-title>
              <v-card-text>
                   <div id="solicitud"> <br><br>
                                  <header>
                            <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css" integrity="sha384-Vkoo8x4CGsO3+Hhxv8T/Q5PaXtkKtu6ug5TOeNV6gBiFeWPGFN9MuhOf23Q9Ifjh" crossorigin="anonymous">
                          <div id="logo">
                              <img src="data:image/png;base64," id="imagen">
                          </div>
                        
                        
                          <br>
                          <div >

                          </div>  
                          <br>
                          <div id="fact">           
                            Carmen, Campeche. a:{{fecha}}<br><br>
                           
                          </div>
                  </header>
                  
                                             <strong v-if="reporteCategoria=='Por Usuario' || reporteCategoria=='Por Fecha'">Reportes de Cambios de Guardia del {{fechaR1}} a {{fechaR2}} </strong>
                                             <strong v-if="reporteCategoria=='Por Embarcacion'" >Reportes de Cambios de Guardia del {{fechaR1}} a {{fechaR2}} con la Embarcación {{embarcacionDetalle}}</strong>
                                             <strong v-if="reporteCategoria=='Por Estado'" >Reportes de Cambios de Guardia del {{fechaR1}} a {{fechaR2}} con estatus {{estado}}</strong>
                                             <strong v-if="reporteCategoria=='Por Muelle'" >Reportes de Cambios de Guardia del {{fechaR1}} a {{fechaR2}} en el muelle {{muelle}}</strong>

                  
                            <div style="" >

                           <div class="row" v-if="reporteCategoria=='Por Usuario'">
                        <div class="col-md-8">
                          <table class="table table-sm">
                          
                            <tbody>
                              <tr>   
                                <td>Nombre</td>
                                <td><strong>{{usuarioDetalle.nombre}}</strong></td> 
                                <td>Agencia</td>
                                <td><strong>{{usuarioAgencia.nombre}}</strong></td>
                              </tr>
                              <tr>
                                <td>Dirección</td>
                                <td><strong>{{usuarioDetalle.direccion}}</strong></td>
                                <td>RFC</td>
                                <td><strong>{{usuarioAgencia.rfc}}</strong></td>
                              </tr>
                              <tr> 
                                <td>Email</td>
                                <td><strong>{{usuarioDetalle.email}}</strong></td>
                                <td>Representante</td>
                                <td><strong>{{usuarioAgencia.representante}}</strong></td>
                              </tr>
                              <tr> 
                               <td>Telefono</td>
                                <td><strong>{{usuarioDetalle.telefono}}</strong></td>
                                <td></td>
                                <td><strong></strong></td>
                              </tr>
                            
                            </tbody>
                          </table>
                        </div>
                    </div>
                            </div>
                            
                     <div class="row">
                      <div class="col-md-12">
                         

                             <table class="table table-sm"  v-if="reporteCategoria=='Por Usuario'">
                             
                              <thead>
                                <tr>
                                  <th scope="col">#</th>
                                  <th scope="col">Compañia</th>
                                  <th scope="col">Correo</th>
                                  <th scope="col">Embarcación</th>
                                  <th scope="col">Muelle</th>
                                  <th scope="col">Pasajeros</th>
                                  <th scope="col">Destino</th>
                                  <th scope="col">Detalles</th>
                             
                                  
                                  
                                </tr>
                              </thead>
                              <tbody v-for="(det, index) in cambiosGuardia" :key="det._id">
                                 <tr >   
                                          <td>{{index+1}}</td>
                                          <td>{{det.usuario.empresa}}</td>
                                          <td>{{det.usuario.email}}</td>
                                          <td>{{det.embarcacion.nombre}}</td>
                                          <td>{{det.muelle}}</td>
                                          <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.nombreCompleto}}
                                            </li>
                                          </td>
                                            <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.destino}}
                                            </li>
                                          </td>
                                           <td>{{det.detalles}}</td>
                                        
                                  </tr>
                                  
                              </tbody>
                             
                                
                            </table>

                              <table class="table table-sm"  v-if="reporteCategoria=='Por Fecha' || reporteCategoria=='Por Embarcacion'"> 
                             
                              <thead>
                                <tr>
                                  <th scope="col">#</th>
                                  <th scope="col">Usuario</th>
                                  <th scope="col">Correo</th>
                                  <th scope="col">Embarcación</th>
                                  <th scope="col">Muelle</th>
                                  <th scope="col">Pasajeros</th>
                                  <th scope="col">Destino</th>
                                  <th scope="col">Detalles</th>
                             
                                  
                                  
                                </tr>
                              </thead>
                              <tbody v-for="(det, index) in cambiosGuardia" :key="det._id">
                                 <tr >   
                                          <td>{{index+1}}</td>
                                          <td>{{det.usuario.nombre}}</td>
                                          <td>{{det.usuario.email}}</td>
                                          <td>{{det.embarcacion.nombre}}</td>
                                          <td>{{det.muelle}}</td>
                                          <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.nombreCompleto}}
                                            </li>
                                          </td>
                                            <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.destino}}
                                            </li>
                                          </td>
                                           <td>{{det.detalles}}</td>
                                        
                                  </tr>
                                  
                              </tbody>
                             
                                
                            </table>
                             <table class="table table-sm"  v-if="reporteCategoria=='Por Estado'"> 
                             
                              <thead>
                                <tr>
                                  <th scope="col">#</th>
                                  <th scope="col">Usuario</th>
                                  <th scope="col">Correo</th>
                                  <th scope="col">Embarcación</th>
                                  <th scope="col">Muelle</th>
                                  <th scope="col">Pasajeros</th>
                                  <th scope="col">Destino</th>
                                  <th scope="col">Detalles</th>
                             
                                  
                                  
                                </tr>
                              </thead>
                              <tbody v-for="(det, index) in cambiosGuardia" :key="det._id">
                                 <tr >   
                                          <td>{{index+1}}</td>
                                          <td>{{det.usuario.nombre}}</td>
                                          <td>{{det.usuario.email}}</td>
                                          <td>{{det.embarcacion.nombre}}</td>
                                          <td>{{det.muelle}}</td>
                                          <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.nombreCompleto}}
                                            </li>
                                          </td>
                                            <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.destino}}
                                            </li>
                                          </td>
                                           <td>{{det.detalles}}</td>
                                        
                                  </tr>
                                  
                              </tbody>
                             
                                
                            </table>
                             <table class="table table-sm"  v-if="reporteCategoria=='Por Muelle'"> 
                             
                              <thead>
                                <tr>
                                  <th scope="col">#</th>
                                  <th scope="col">Usuario</th>
                                  <th scope="col">Correo</th>
                                  <th scope="col">Embarcación</th>
                                  <th scope="col">Muelle</th>
                                  <th scope="col">Pasajeros</th>
                                  <th scope="col">Destino</th>
                                  <th scope="col">Detalles</th>
                             
                                  
                                  
                                </tr>
                              </thead>
                              <tbody v-for="(det, index) in cambiosGuardia" :key="det._id">
                                 <tr >   
                                          <td>{{index+1}}</td>
                                          <td>{{det.usuario.nombre}}</td>
                                          <td>{{det.usuario.email}}</td>
                                          <td>{{det.embarcacion.nombre}}</td>
                                          <td>{{det.muelle}}</td>
                                          <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.nombreCompleto}}
                                            </li>
                                          </td>
                                            <td>
                                            <li v-for="(dets) in det.pasajeros" :key="dets._id">
                                                 {{dets.destino}}
                                            </li>
                                          </td>
                                           <td>{{det.detalles}}</td>
                                        
                                  </tr>
                                  
                              </tbody>
                             
                                
                            </table>

                         
                      </div>
                     </div>

                        <div class="row">
                      <div class="col-md-8">
                        
                      
                         
                      </div>
                     </div>

                  </div>
                  
              </v-card-text>
              <v-card-actions>
                <v-btn @click="ocultarComprobante()" small>Cancelar</v-btn>
              </v-card-actions>
            </v-card>

          </v-dialog>
  </v-flex>
 </v-layout>
</template>
<script>

import axios from 'axios';
import DatetimePicker from 'vuetify-datetime-picker'
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'

  export default {
     icons: {
    iconfont: 'fa4',
  },
    data(){
      return{
                  dialog: false,
                  search: '',
                 monitoreo:[],
                headers: [
               { text: 'Actions', value: 'action', sortable: false},
               { text: 'Estado', value: 'estado', sortable:true},
               { text: 'Muelle', value: 'servicio.muelle', sortable:true},
               { text: 'Programa', value: 'servicio.programa', sortable:true},
             
            ],
            
            editedIndex: -1,
            _id:'',
            embarcacion:'',
            embarcaciones:[],
            fecha:'',
            colores:['cyan','green','pink','amber','orange'],
            color:'',
            servicios:[],
            servicio:'',
            reporteCategoria:'',
            usuarioDetalle:[],
            reportCat:'',
            loader: null,
            loading: false,
            tipo_reporte:['Cambios de Guardia','Solicitud Unica de Acceso','Accesos a Vehiculos y Personal','Embarcaciones','Agencias','Usuarios'],
            reporteCG:['Por Usuario','Por Fecha','Por Embarcacion','Por Estatus', 'Por Muelle','Por Agencia'],
            reporteSUA:['Por Agencia','Por Solicitante','Por Fecha','Por Muelle','Por Embarcacion',],
            reporteAVP:['Por Fecha','Por Embarcacion','Por Agencia','Por Muelle','Por Solicitante'],
            reporteAgencias:['Agencias Activas','Agencias Inactivas'],
            reporteEmbarcaciones:['Embarcaciones Activas','Embarcaciones Inactivas'],
            tipo:'',
            agencias:[],
            nombre:'',
            agencia2:'',
            cabeceraDetalles:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Color', value: 'color' },
              { text: 'Fecha y Hora', value: 'fecha' },
              { text: 'Tipo de Movimiento', value: 'tipoMovimiento' },
              { text: 'Detalles', value: 'detalles' },
           
            ],
            movimientos:[],
            verNuevo:0,
            usuario:'',
            tipoMovimiento:'',
            detalles:'',
            fecha:'',
            muelles:['FRONTERA','CHILTEPEC','SANCHEZ MAGALLANEZ'],
            folio:'',
            fecha1:'',
            fecha2:'',
            cambiosGuardia:[],
            estado:'',
            detalles:'',
            usuarioAgencia:[],
            compania:'',
            usuarios:[],
            muelle:'',
            fechaR1:'',
            agencia:'',
            agencias:[],
            embarcacionDetalle:[],
            estados:['En Proceso','Cancelado','Pendiente'],
            reportEmb:'',
            fechaR2:'',
            tipoReporte:'',
            tipoRep:'',
            valida:0,
            fecha:null,
            validaMensaje:[],
            adModal:0,
            comprobanteModal:0,
            adAccion:0,
            adNombre:'',
            adId:''
      }
    },
          computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'Nueva Solicitud' : 'Editar Solicitud'
          }
        },

        watch: {
          dialog (val) {
            val || this.close()
          }
        },

        created () {
       this.selectUsuarios()
       this.selectEmbarcaciones()
        },
        watch: {
      loader () {
        const l = this.loader
        this.mostrarComprobante()
        this[l] = !this[l]

        setTimeout(() => (this[l] = false), 1000)
         
        this.loader = null
       
      },
    },

        methods: {

        

       

          mostrarNuevo(){
            this.verNuevo =1;

          },
          ocultarNuevo(){
            this.verNuevo =0
            this.limpiar()
          },

      
       


       

     
          limpiar(){
            this._id=''
           
            this.usuario='',
            this.folio='',
            this.agencia='',
            this.fecha='',
            this.pasajeros=[],
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
          },
         
        
           serviciosChange(){
             this.tipoRep = this.tipoReporte 
            this.reporteCategoria = ''
            },

            reporteServiciosChange(){
             this.reportCat = this.reporteCategoria
           
            },

        
        
          listarDetalles(){
            if(this.reporteCategoria=='Por Usuario'){
                let header={"Token":this.$store.state.token};
                let configuracion= {headers: header}
                let me=this;
                axios.get('cambioGuardia/listCGUsuario?usuario='+this.usuario+'&fecha1='+this.fecha1+'&fecha2='+this.fecha2,configuracion).then(function (response){
                me.cambiosGuardia=response.data;
                }).catch(function(error){
                  console.log(error)
                })

                this.detallesUsuarios()
            }
            else if(this.reporteCategoria=='Por Fecha'){
                let header={"Token":this.$store.state.token};
                let configuracion= {headers: header}
                let me=this;
                axios.get('cambioGuardia/listFecha?fecha1='+this.fecha1+'&fecha2='+this.fecha2,configuracion).then(function (response){
                me.cambiosGuardia=response.data;
                }).catch(function(error){
                  console.log(error)
                })
            }
             else if(this.reporteCategoria=='Por Embarcacion'){
                let header={"Token":this.$store.state.token};
                let configuracion= {headers: header}
                let me=this;
                axios.get('cambioGuardia/listCGEmbarcacion?embarcacion='+this.embarcacion+'&fecha1='+this.fecha1+'&fecha2='+this.fecha2,configuracion).then(function (response){
                me.cambiosGuardia=response.data;
                }).catch(function(error){
                  console.log(error)
                })

               this.detallesEmbarcacion()
            }
             else if(this.reporteCategoria=='Por Estado'){
                let header={"Token":this.$store.state.token};
                let configuracion= {headers: header}
                let me=this;
                var status
                if(this.estado=='En Proceso'){
                  status=0
                }else if(this.estatus=='Aprobado'){
                  status = 1
                }else{
                  status=2
                }
                axios.get('cambioGuardia/listCGEstado?estado='+status+'&fecha1='+this.fecha1+'&fecha2='+this.fecha2,configuracion).then(function (response){
                me.cambiosGuardia=response.data;
                }).catch(function(error){
                  console.log(error)
                })
            }
              else if(this.reporteCategoria=='Por Muelle'){
                let header={"Token":this.$store.state.token};
                let configuracion= {headers: header}
                let me=this;
                axios.get('cambioGuardia/listCGMuelle?muelle='+this.muelle+'&fecha1='+this.fecha1+'&fecha2='+this.fecha2,configuracion).then(function (response){
                me.cambiosGuardia=response.data;
                }).catch(function(error){
                  console.log(error)
                })

            }
          },
           detallesUsuarios(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('usuario/query?_id='+this.usuario,configuracion).then(function (response){
            me.usuarioDetalle=response.data;
            me.usuarioAgencia=response.data.agencia
            console.log(me.usuarioDetalle)
            }).catch(function(error){
              console.log(error)
            })
          },
          detallesEmbarcacion(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('embarcacion/query?_id='+this.embarcacion,configuracion).then(function (response){
            me.embarcacionDetalle=response.data;
            console.log(me.embarcacionDetalle)
            }).catch(function(error){
              console.log(error)
            })
          },

           mostrarComprobante(){
            var options = { year: 'numeric', month: 'long', day: 'numeric' };
            var fec = new Date();   
            this.fecha = fec.toLocaleString(); 

            this.fechaR1 = this.fecha1.toLocaleString(); 
            this.fechaR2 = this.fecha2.toLocaleString();
            this.listarDetalles(),
            this.comprobanteModal=1          
          },
           ocultarComprobante(item){
            this.comprobanteModal=0
          },

         selectEmbarcaciones(){
            let me = this;
            let embarcacionArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('embarcacion/list',configuracion).then((response)=>{
              embarcacionArray=response.data;
              embarcacionArray.map(function(x){
                me.embarcaciones.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },

          
        selectAgencias(){
            let me = this;
            let agenciaArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('agencia/list',configuracion).then((response)=>{
              agenciaArray=response.data;
              agenciaArray.map(function(x){
                me.agencias.push({text:x.agencia, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },

          selectUsuarios(){
            let me = this;
            let usuarioArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('usuario/list',configuracion).then((response)=>{
              usuarioArray=response.data;
              usuarioArray.map(function(x){
                me.usuarios.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },


        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>
<style>
  #factura {
            padding: 20px;
            font-family: Arial, sans-serif;
            font-size: 16px ;
        }

        #logo {
            float: left;
            margin-left: 2%;
            margin-right: 2%;
        }
         #logo2 {
           
            margin-left: 80%;
            margin-right: 2%;
        }
        #table_agencia {
           margin-right: 10px;
        }
        #imagen {
            width: 100px;
        }
         #imagen2{
