<template>
  <v-layout align-start>
  <v-flex>
    <v-data-table
      :headers="headers"
      :items="cambiosGuardia"
      :search="search"
      class="elevation-1"
      v-if="verNuevo==0"
    >
      <template v-slot:item.estado="{ item }">
         <v-chip small class="ma-2" color="orange" text-color="white"  v-if="item.estado==0">
          <v-avatar left>
            <v-icon small>restore</v-icon>
          </v-avatar>
            En proceso
         </v-chip>

        <v-chip small class="ma-2" color="primary" text-color="white"  v-if="item.estado==1">
          <v-avatar left>
            <v-icon small>done</v-icon>
          </v-avatar>
            Aprobado AMLS
        </v-chip>

        <v-chip small class="ma-2" color="error" text-color="white"  v-if="item.estado==2">
          <v-avatar left>
            <v-icon small>error</v-icon>
          </v-avatar>
            {{item.motivo_cancelacion}}
         </v-chip>
    </template>
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Embarque de Personal y Pasajeros</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-text-field class="text-xs-center" v-if="verNuevo==0" v-model="search" append-icon="search" label="Busqueda" single-line hide-details></v-text-field>
          <v-spacer></v-spacer>
         <v-btn v-if="verNuevo==0" @click="mostrarNuevo()"  small>Nueva Solicitud</v-btn>
          
        
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{  }">
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
  
              <v-card-text>
                <v-container>
                  <v-row>
                   
                   
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="guardar">Guardar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="adModal" max-width="290">
            <v-card>
              <v-card-title class="headline" v-if="adAccion==1">
                  Activar Item
              </v-card-title>
              <v-card-title class="headline" v-if="adAccion==2">
                  Desactivar Item
              </v-card-title>

              <v-card-text>
                  Estas a punto de <span v-if="adAccion==1">Activar</span>
                  <span v-if="adAccion==2">Desactivar</span> la solicitud con folio {{adNombre}}
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                  Cancelar
                </v-btn>
                 <v-btn v-if="adAccion==1" @click="activar()" color="orange darken-4" flat="flat">
                   Aprobar
                </v-btn>
                <v-btn v-if="adAccion==2" @click="desactivar()" color="orange darken-4" flat="flat">
                  Desactivar
                </v-btn>

              </v-card-actions>

            </v-card>
          </v-dialog>
       <v-dialog v-model="comprobanteModal" max-width="1100px">
            
            <v-card>
              <v-card-title class="headline">
                  <v-btn @click="crearPDF()"><v-icon>print </v-icon> </v-btn> Reporte de Entrada
              </v-card-title>
              <v-card-text>
                   <div id="solicitudGuardia"> <br><br>
                                  <header>
                         <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css" integrity="sha384-Vkoo8x4CGsO3+Hhxv8T/Q5PaXtkKtu6ug5TOeNV6gBiFeWPGFN9MuhOf23Q9Ifjh" crossorigin="anonymous">
                          <div id="logo">
                              <img src="data:image/png;base64," id="imagen">
                          </div>
                           <div id="logo2">
                             <img id="imagen2" src="data:image/jpeg;base64,">
                          </div>
                          <div id="datos">
                              <p id="encabezado">
                                  <strong id="texto">Solicitud de Entrada</strong><br>
                                    <strong id="texto">VP{{agenciasUsuario.folio}}-{{counter}}</strong><br>

                              </p>
                          </div>
                          <br>
                        
                          <br>
                           <div id="texto" style="margin-left: 66%;">
                            Frontera, Tab. a: {{fecha}}
                          </div>
                           
                          
                        
                      </header>
                      <br>
                    <div id="agencia">
                        <p id="texto">De conformidad y en cumplimiento a lo estipulado en las Reglas de Operación de la APITAB en su capitulo IV.- "Accesi y
                          vialidades del Puerto" Regla 19, 20, 21 y 22 y Código Internacional de Protección a Buques e Instalaciones Portuarias.</p>  
                   
                      <p id="texto">Solicito la <strong>ENTRADA</strong> a las areas del recinto portuario concesionadas a la administracion portuaria 
                         integral de Tabasco S.A. de C.V con Fecha <strong>{{fecha_solicitud}}</strong> del personal y vehiculos que a continuacion se enlistan</p>   
                    </div>

                   <div id="agencia">
                      <div class="row">        
                        <v-col cols="4">
                          <v-subheader  class="pa-0">Nombre del Solicitante</v-subheader>
                            Cargo<br>
                            <v-subheader class="pa-0">Agencia Naviera</v-subheader>
                          
                        </v-col>
                        <v-col cols="8">
                          <v-text-field  class="pa-0" v-model="usuario.nombre" readonly  hide-details></v-text-field>
                          <v-text-field class="pa-0" v-model="usuario.cargo" readonly  hide-details></v-text-field>
                          <v-text-field class="pa-0" v-model="agenciasUsuario.nombre" readonly  hide-details></v-text-field>              
                        </v-col>
                      </div>
                             <table class="table table-sm">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Servicios portuarios requeridos</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                    <td >Prestador de Servicios</td>
                                    <td>
                                        <span v-if="tipo=='Prestador de Servicios'" style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="tipo=='Cesionario'" style="border-image: initial; color:white; border: 1px solid black;">______</span>                      

                                    </td>
                                    <td>Cesionario</td>
                                    <td>
                                        <span v-if="tipo=='Cesionario'"  style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="tipo=='Prestador de Servicios'" style="border-image: initial; color:white; border: 1px solid black;">______</span>                                                        </td>
                                 
                                  
                                </tr>
                                 
                                  <tr>      
                                   
                                  </tr>
                            
                              </tbody>
                              
                            </table>

                             

                                  
                             <table class="table table-sm">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="11">Características de la Embarcación</th>         
                                </tr>
                              </thead>
                              <tbody >
     
                                  <tr>
                                    <td colspan="3" style="border-color:white">Nombre de la embarcación</td>
                                    <td colspan="8">{{embarcacion.nombre}}</td>
                                    
                                 
                                  </tr>
                                   <tr>
                                    <td colspan="3" style="border-color:white"></td>
                                    <td colspan="8"></td>  
                                  </tr>
                            
                              </tbody>

                                <tbody style="border-color:white">
     
                                  <tr>
                                    <td colspan="3" style="border-color:white">Al servicio de la compañia</td>
                                    <td colspan="8">{{compania.razon_social}}</td>
                                    
                                 
                                  </tr>
                                   <tr>
                                    <td colspan="3" style="border-color:white"></td>
                                    <td colspan="8"></td>
                                    
                                 
                                  </tr>
                            
                              </tbody>

                              <tbody style="border-color:white">
                               
                               
                                  <tr>
                                    <td style="border-color:white">Tipo</td>
                                    <td>{{embarcacion.tipo}}</td>
                                    
                                    <td >Bandera</td>
                                    <td>{{embarcacion.bandera}}</td>
                                    <td >T.R.B.</td>
                                    <td>{{embarcacion.tbr}}</td>
                                    <td >Eslora</td>
                                     <td>{{embarcacion.eslora}}</td>
                                     <td >Manga</td>
                                     <td>{{embarcacion.manga}}</td>
                                    
                                    <td></td>
                                  </tr>
                                  <tr>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                    <td style="border-color:white"></td>
                                    <td></td>
                                     <td style="border-color:white"></td>
                                    <td></td>
                                  </tr>
                                  
                           

                           
                            
                              </tbody>
                            </table>
                                <table class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Lista de Personal</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                     <td >#</td>
                                    <td >Nombre</td>
                                    <td >Compañia</td>
                                    <td >Puesto</td>
                                    <td >No. de Licencia</td>
                                </tr>
                                <tr v-for="(det, index) in pasajeros" :key="det._id">   
                                          <td>{{index+1}}</td>
                                         
                                          <td>{{det.nombreCompleto}}</td>
                                           <td>{{det.compania}}</td>
                                           <td>{{det.puesto}}</td>
                                          <td></td>
                                      </tr>
                              </tbody>
                            </table>

                             

                            

                              <table  class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE ">
                                <tr>
                                  <th colspan="8">Vehiculos</th>         
                                </tr>
                              </thead>
                              <tbody >
                                <tr >
                                    
                                    <td >Marca</td>
                                    <td >Modelo</td>
                                    <td >Color</td>
                                    <td >Placas</td>
                                    <td >Compañia de aseguradora</td>
                                    <td >Vigencia de seguro</td>
                                </tr>
                                <tr>   
                                          <tr v-for="(det) in vehiculos" :key="det._id">   
                                         
                                         
                                          <td>{{det.marca}}</td>
                                           <td>{{det.tipo}}</td>
                                           <td>{{det.color}}</td>
                                             <td>{{det.placa}}</td>
                                           <td>{{det.compania_aseguradora}}</td>
                                           <td>{{det.vigenciaSeguro}}</td>
                                      </tr>
                                     
                              </tbody>
                            </table>

                             <table class="table table-sm">
                            
                              <tbody >
                                <tr>
                                    <td style="border-color:white" >Ingreso de material o equipo</td>
                                    <td style="border-color:white">
                                        <span v-if="ingreso_mat" style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="!ingreso_mat" style="border-image: initial; color:white; border: 1px solid black;">______</span>                      

                                    </td>
                                    <td style="border-color:white">Retiro de material o equipo</td>
                                    <td style="border-color:white">
                                        <span v-if="retiro_mat"  style="border-image: initial;color:black;  background-color:black ">______</span>
                                        <span v-if="!retiro_mat" style="border-image: initial; color:white; border: 1px solid black;">______</span>                                                        </td>
                                 
                                  
                                </tr>
                                 
                                  <tr style="border-color:white">       
                                   
                                  </tr>
                            
                              </tbody>
                              
                            </table>

                              <table class="table table-sm table-bordered">
                              <thead style="background-color:#EFEEEE">
                                <tr>
                                  <th colspan="8">Motivo de la visita</th>         
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                   
                                    <td >{{detalles}}</td>
                                  
                                </tr>
                               
                              </tbody>
                            </table>


                   </div> 
                 
                   
                  </div>
              </v-card-text>
              <v-card-actions>
                <v-btn @click="ocultarComprobante()" small>Salir</v-btn>
              </v-card-actions>
            </v-card>

          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.action="{ item }">

       <v-icon
        v-if="item.estado==0"
          small
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>

      

    
       
        <v-icon
        v-if="item.estado==2"
          
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>
       
          <v-icon
        v-if="item.estado==3"
          
          class="mr-2"
          @click="mostrarComprobante(item)"
        >
          text_snippet
        </v-icon>
       

      </template>
        <template v-slot:item.manifiesto="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="descargarManifiesto(item)"
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

        <template v-slot:item.personal_adj="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="descargarPersonal(item)" 
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

       <template v-slot:item.vehiculos_adj="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="descargarVehiculos(item)" 
        >
fa fa-file-pdf-o
        </v-icon>
      </template>

        <template v-slot:item.fecha="{ item }">
          {{  item.fecha.join(" al ")}}
      </template>
   
      <template v-slot:item.folio="{ item }">
        VP{{item.counter}}
      </template>
      
      <template v-slot:no-data>
        <v-btn color="primary" @click="listar()">Resetear</v-btn>
      </template>
    </v-data-table>

    <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-layout row wrap>

           <v-flex xs11 sm11 lg11 x11>
              <div>
              

                <v-expansion-panels
                  v-model="panel"
                  multiple
                >
                  <v-expansion-panel>
                    <v-expansion-panel-header>Datos de la Agencia</v-expansion-panel-header>
                    <v-expansion-panel-content>
                         <v-row no-gutters>
                             <v-col cols="4">
                                  <v-subheader class="pa-0">Agencia Consignataria</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                 <v-text-field class="pa-0" v-model="agenciasUsuario.nombre" readonly  hide-details></v-text-field>
                              </v-col>

                               <v-col cols="4">
                                  <v-subheader class="pa-0">Direccion</v-subheader>
                              </v-col>

                                <v-col cols="8" class="text--secondary">
                                 <v-text-field class="pa-0" v-model="agenciasUsuario.direccion" readonly  hide-details></v-text-field>
                              </v-col>
                               <v-col cols="4">
                                  <v-subheader class="pa-0">RFC</v-subheader>
                              </v-col>

                                <v-col cols="8" class="text--secondary">
                                 <v-text-field class="pa-0" v-model="agenciasUsuario.rfc" readonly  hide-details></v-text-field>
                              </v-col>

                                <v-col cols="4">
                                  <v-subheader class="pa-0">Email</v-subheader>
                              </v-col>

                                <v-col cols="8" class="text--secondary">
                                 <v-text-field class="pa-0" v-model="agenciasUsuario.email" readonly  hide-details></v-text-field>
                              </v-col>

                                 <v-flex xs6 sm6 lg6 x6>
                                    <v-radio-group v-model="tipo" row>
                                          <v-radio label="Prestador de Servicios" value="Prestador de Servicios"></v-radio>
                                          <v-radio label="Cesionario" value="Cesionario"></v-radio>
                                    </v-radio-group>              
                                 </v-flex>
                         </v-row>
                         
                    </v-expansion-panel-content>
                  </v-expansion-panel>


                  <v-expansion-panel>
                    <v-expansion-panel-header>Datos de la solicitud</v-expansion-panel-header>
                    <v-expansion-panel-content>
                              <v-row no-gutters>
                              
                              <v-col cols="4">
                                  <v-subheader class="pa-0">Periodo de la estadia</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                 <v-dialog
                                    ref="dialog"
                                    v-model="modal"
                                    :return-value.sync="date"
                                    persistent
                                    width="290px"
                                  >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="dateRangeText"
                                  label="Periodo de Estadia"
                                  prepend-icon="event"
                                  readonly
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker v-model="dates" range scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.dialog.save(date)">OK</v-btn>
                              </v-date-picker>
                            </v-dialog>                     
                          </v-col>

                              <v-col cols="4">
                                  <v-subheader class="pa-0">Muelle de Arribo</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                   <v-autocomplete :items="muelles"   v-on="on" class="pa-0" hide-details v-model="muelle" ></v-autocomplete>
                              </v-col>

                                <v-col cols="8" class="text--secondary">
                                   <v-flex xs1                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                 2 sm12 lg12 x12>
                                        <v-container >
                                      <v-checkbox v-model="ingreso_mat" label="Ingreso de material o equipo"></v-checkbox>
                                      <v-checkbox v-model="retiro_mat" label="Retiro de material o equipo"></v-checkbox>
                                    </v-container>
                                </v-flex>
                              </v-col>

                            
                      
                         </v-row>
                         
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header>Características de la embarcacion</v-expansion-panel-header>
                    <v-expansion-panel-content>
                              <v-row no-gutters>
                              
                              <v-col cols="4">
                                  <v-subheader class="pa-0">Nombre de la embarcación</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                  <v-autocomplete  v-model="embarcacion"  class="pa-0" hide-details :items="embarcaciones"  ></v-autocomplete>
                              </v-col>

                              <v-col cols="4">
                                  <v-subheader class="pa-0">Al servicio de la compañia</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                    <v-autocomplete :items="companias"  class="pa-0" hide-details  v-model="compania" ></v-autocomplete>
                              </v-col>
                              
                           
                         </v-row>
                         
                    </v-expansion-panel-content>
                  </v-expansion-panel>

                    <v-expansion-panel>
                    <v-expansion-panel-header>Lista del Personal</v-expansion-panel-header>
                    <v-expansion-panel-content>
                              <v-row no-gutters>
                                <v-col cols="12" class="text--secondary">
                                   <v-flex xs12 sm2 md2 lg2 x12>
                                        <v-btn small fab dar color="" @click="agregarDetalle()">
                                            <v-icon dark>group_add
                                        </v-icon>
                                        </v-btn>
                                    </v-flex>

                                          <v-flex xs12 sm12 md12 lg12 xl12>
             
                                              <v-data-table
                                                :headers="cabeceraDetalles"
                                                :items="pasajeros"
                                                class="elevation-1"
                                                hide-default-footer
                                              
                                              >
                                                      <template slot="items" slot-scope="props">
                                                        <td class="text-xs-center"><v-text-field v-model="props.items.nombreCompleto"></v-text-field></td>
                                                    
                                                        <td class="text-xs-center"><v-text-field v-model="props.items.compania"></v-text-field></td>

                                                        <td class="text-xs-center"><v-text-field v-model="props.items.puesto"></v-text-field></td>
                                                        <td class="text-xs-center"><v-text-field v-model="props.items.licencia"></v-text-field></td>

                                                      </template>
                                          <template  v-slot:item.borrar="{ item }">
                                              <v-icon
                                                  small
                                                  @click="eliminarPasajero(pasajeros,item)"
                                                  >
                                                  delete
                                              </v-icon>
                                          </template>

                                            

                                          <template  v-slot:item.nombreCompleto="{ item }">
                                              <td class="text-xs-center"><v-text-field clearable="clearable" v-model="item.nombreCompleto"></v-text-field></td>
                                          </template>
                                        
                                          <template  v-slot:item.compania="{ item }">
                                            <td class="text-xs-center"><v-text-field clearable="clearable" v-model="item.compania"></v-text-field> </td>
                                          </template>
                                        <template  v-slot:item.puesto="{ item }">
                                            <td class="text-xs-center"><v-text-field clearable="clearable" v-model="item.puesto"></v-text-field> </td>
                                          </template>
                                          <template  v-slot:item.licencia="{ item }">
                                            <td class="text-xs-center"><v-text-field clearable="clearable" v-model="item.licencia"></v-text-field> </td>
                                          </template>
                                          
                                      

                                              </v-data-table>
                                          </v-flex>
                                   </v-col>
                           
                         </v-row>
                         
                    </v-expansion-panel-content>
                  </v-expansion-panel>


