                                 <v-flex xs12 sm2 md2 lg2 x12>
                                    

                                
                              </v-flex>
                                </v-col>

                                  <v-col cols="12" class="text--secondary">
                                <v-data-table
                  :headers="cabeceraVehiculos"
                  :items="vehiculos"
                  class="elevation-1"
                  hide-default-footer
                
                 >
