               axios.get('vehiculoPersonal/getAccesoPersonal/'+item.personal_adj, {responseType: 'arraybuffer'}).then(function (response){
               let blob = new Blob([response.data],{ type:'content-type'});
               let link = document.createElement('a');
               link.href = window.URL.createObjectURL(blob);
               link.download = "personal.pdf";
               link.click();
            }).catch(function(error){
              console.log(error)
            })
          },
             downloadVehiculo(item){
               axios.get('vehiculoPersonal/getAccesoVehiculo/'+item.vehiculos_adj, {responseType: 'arraybuffer'}).then(function (response){
               let blob = new Blob([response.data],{ type:'content-type'});
               let link = document.createElement('a');
               link.href = window.URL.createObjectURL(blob);
               link.download = "personal.pdf";
               link.click();
            }).catch(function(error){
              console.log(error)
            })
          },

        activarDesactivarMostrar(accion,item){
          this.adModal=1;
          this.adNombre=item.nombre;
          this.adId=item._id;
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
          }else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/activate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/deactivate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>

