               { text: 'Fecha', value: 'fecha', sortable:false},
               { text: 'Embarcacion', value: 'embarcacion.nombre',sortable:false},
               { text: 'Estado', value: 'estado',sortable:true},

                { text: 'Adjunto', value: 'adjunto'}
            ],
            editedIndex: -1,
            _id:'',
            embarcacion:'',
            embarcaciones:[],
             agenciasUsuario:[],
               muelle:'',
               usuario:'',
            nombreCompleto:'',
            agencias:[],
            folio:'',
            fecha_solicitud:'',
            nombre:'',
            agencia2:'',
            cabeceraDetalles:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Nombre Completo', value: 'nombreCompleto' },
              { text: 'Identificacion', value: 'identificacion' },
              { text: 'Seguro Social', value: 'nss' },
              { text: 'Destino', value: 'destino' },
           
            ],
            pasajeros:[],
            verNuevo:0,
            verDetallee:0,
            fecha:'',
            estado:'',
            detalles:'',
              aprobacionAmls:'',
              razon_social:'',
              operacion:'',
              tipo:'',
              compania:'',
              ingreso_mat:'',
              retiro_mat:'',

            aprobacionApi:'',
            motivo_cancelacion:'',
            valida:0,
            validaMensaje:[],
            adModal:0,
            comprobanteModal:0,
            adAccion:0,
            adNombre:'',
            adId:''
      }
    },
          computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'Nueva Cambio de Guardia' : 'Editar Cambio de Guardia'
          }
        },

        watch: {
          dialog (val) {
            val || this.close()
          }
        },

        created () {
          this.listar();
          this.selectEmbarcaciones();
          this.selectAgencias();
        },

        methods: {
          mostrarNuevo(){
            this.verNuevo =1;
          },
          ocultarNuevo(){
            this.verNuevo =0
            this.limpiar()
          },
 descargarCG(item){
                 window.open(item.adjunto, '_blank');
          },
            crearPDF(){
            var quotes = document.getElementById('solicitudGuardia')
             html2canvas(quotes).then(function (canvas) {
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 200;
                    var pageHeight = 305;

                    var imgHeight = canvas.height * imgWidth / canvas.width;
                    var heightLeft = imgHeight;
                    
                    var doc = new jsPDF('p', 'mm', 'a4');
                    var position = 0;

                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                    heightLeft -= pageHeight;

                    while (heightLeft >= 0) {
                        position = heightLeft - imgHeight;
                        doc.addPage();
                        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                        heightLeft -= pageHeight;
                    }
                    doc.save('solicitudCG.pdf');
            })
          },

            mostrarComprobante(item){
             this.limpiar();
            this.folio = item.folio
            this._id = item._id
            this.aprobacionAmls = item.aprobacionAmls
            this.detalles = item.detalles
            this.aprobacionApi = item.aprobacionApi
            this.usuario = item.usuario
            this.muelle = item.muelle
            this.operacion= item.operacion
            this.compania = item.compania
             if(item.tipo=='Contratista'){
              this.tipo='Contratista'
            }else{
              this.tipo='Armador'
            }

            var fechaAprobacion = item.fechaAprobacion
            var fechaActual = new Date()
            var fechaAprobacionDate = new Date(fechaAprobacion);
            var minutoSumar = 5;

            fechaAprobacionDate.setMinutes(fechaAprobacionDate.getMinutes() + minutoSumar);

            if(fechaActual>=fechaAprobacionDate){
                this.bandera = true
               
            }else{
              this.bandera = false
              
              
            }

             this.ingreso_mat=item.ingreso_mat
            this.retiro_mat=item.retiro_mat
            this.estado = item.estado
            this.compania = item.compania

            this.fecha_solicitud = item.fecha.join(" al ")

            this.listarAgencia(item.usuario.agencia)
            var solic_fec = item.fecha;
            var fecha = new Date();
            var options = { year: 'numeric', month: 'long', day: 'numeric' };
           
            this.tipoServicio = item.tipoServicio
            this.fecha = fecha.toLocaleDateString("es-ES", options);
            this.embarcacion = item.embarcacion
            this.listarDetalles(item._id);
            this.comprobanteModal=1                
          },
           ocultarComprobante(item){
            this.comprobanteModal=0
          },


          eliminarPasajero(arr,item){ 
            let i=arr.indexOf(item);
            if(i!= -1){
              arr.splice(i,1);
            }
          },

          selectEmbarcaciones(){
            let me = this;
            let embarcacionArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('embarcacion/list',configuracion).then((response)=>{
              embarcacionArray=response.data;
              embarcacionArray.map(function(x){
                me.embarcaciones.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },

           selectAgencias(){
            let me = this;
            let agencaiArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('agencia/list',configuracion).then((response)=>{
              agencaiArray=response.data;
              agencaiArray.map(function(x){
                me.agencias.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },


          validar(){
            this.valida=0;
            this.validaMensaje=[];
            if(!this.rol){
              this.validaMensaje.push('El rol es obligatorio');
            }
            if(this.empresa.length<1 || this.empresa.length>30){
              this.validaMensaje.push('El nombre de la empresa debe tener entre 1 y 30 caracteres');
            }
            if(this.nombre.length<1 || this.nombre.length>50){
              this.validaMensaje.push('El nombre del usuario debe tener entre 1 y 50 caracteres');
            }
             if(this.email.length<1 || this.email.length>50){
              this.validaMensaje.push('El email debe tener entre 1 y 50 caracteres');
            }
             if(this.password.length<1 || this.password.length>64){
              this.validaMensaje.push('El password debe tener entre 1 y 64 pasajeros');
            }
           

           
           
            if(this.validaMensaje.length){
              this.valida=1;
            }
            return this.valida;
          },

                listarAgencia(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('agencia/query?_id='+id,configuracion).then(function (response){
            me.agenciasUsuario=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },

          agregarDetalle(){
            this.pasajeros.push(
              {
                nombreCompleto: '',
                identificacion: '',
                nss: '',
                destino:''
              }
            )
          },
          limpiar(){
            this._id=''
            this.usuario='',
            this.agencia='',
            this.fecha='',

            this.pasajeros=[],
          
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
            this.verDetallee=0;
          },
          guardar(){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
          
           
              if(this.editedIndex >-1 ){
              
                axios.put('cambioGuardia/update',
                {
                    '_id':this._id,
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,
                    'detalles':this.detalles,
                    'agencia':this.agencia,
                    'fecha':this.fecha,
                    'pasajeros':this.pasajeros,
                    
                    
               },configuracion)
               .then(function(response){
                    me.close();
                    me.ocultarNuevo();
                    me.listar();
               }).catch(function(error){
                 console.log(error)
               });
              }else{
                axios.post('cambioGuardia/add',
                {
                  
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,
                    'detalles':this.detalles,
                    'agencia':this.agencia,
                    'fecha':this.fecha,
                    'pasajeros':this.pasajeros,
                    'detalles':this.detalles
                    
               },configuracion)
               .then(function(response){
                 me.limpiar();
                   me.ocultarNuevo();
                 me.close();
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
              }
             
          },
          listar(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('cambioGuardia/listAdmin',configuracion).then(function (response){
            me.cambiosGuardia=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },

          listarDetalles(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('cambioGuardia/query?_id='+id,configuracion).then(function (response){
            me.pasajeros=response.data.pasajeros;
            
            }).catch(function(error){
              console.log(error)
            })
          },

          verGuardia(item){
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.detalles = item.detalles
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
            this.verDetallee=1;
          },

          editItem (item) {
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
            this._id=item._id;
            this.folio = item.folio
            this.fecha = item.fecha
            this.detalles = item.detalles
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

        activarDesactivarMostrar(accion,item){
          this.adModal=1;
          this.adNombre=item.folio;
          this.adId=item._id;
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
          }else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('cambioGuardia/deactivateApi',{'_id':this.adId,'aprobacionApi':this.$store.state.usuario.nombre,'motivo_cancelacion':this.motivo_cancelacion},configuracion)
               .then(function(response){
                 swal(
                   "Se ah cancelado la solicitud",
                   "Se ah cancelado la solicitud con exito ",
                   "success"
                 )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('cambioGuardia/deactivateApi',{'_id':this.adId,'aprobacionApi':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error) 
               });
        },

         activarApi(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('cambioGuardia/aprobarApi',{'_id':this.adId,'aprobacionApi':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                 swal(
                   "Se ah aprobado la solicitud",
                   "Se ah aprobado la solicitud con exito ",
                   "success"
                 )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>